@import 'src/assets/clientLogin/scss/utils';
@import "~@dsm/scss/_variables";
$imgWidth:82px;
$imgHeight:60px;

.client-wait {
    &-wrapper{
        width: 100%;
        height: 100%;
    }
    &-sub-wrapper{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    &-hidden{
        display: none!important;
    }
    
    &-img-wrap {
        margin-top: 145px;
    }
    
    &-img {
        @include bg2x('wait', $imgWidth, $imgHeight);
    }
    
    &-not-login-img {
        @include bg2x('unlogin', $imgWidth, $imgHeight);
    }
    
    &-fail-img {
        @include bg2x('fail', $imgWidth, $imgHeight);
    }
    &-title{
        margin-top: 24px;
        font-size: 18px;
        font-weight: 500;
    }
    &-sub-content{
        margin-top: 10px;
        text-align: center;
        font-size:14px;
        font-weight: 400;
        >span{
            color: $color-blackModeAssistText;
        }
        >a{
            color:$color-primary;
            margin-left: 2px;
            cursor: pointer;
        }
    }
    &-btn{
        width: 100%;
        height: 40px;
        font-size: 13px;
        font-weight: 700;
        cursor: pointer;
        border: 1px solid $color-blackModeDark;
        border-radius: 4px;
        text-align: center;
        display:flex;
        align-items: center;
        justify-content: center;
        user-select: none;
        &-cancel{
            width: 72px;
            margin-top: 52px;
        }
        &:hover,
        &:focus{
            background-color:lighten($color-blackModeDark,10%);
            border-color:transparent;
        }
        &:active{
            background:lighten($color-blackModeDark,15%)
        }
    }
    
    &-not-title{
        margin-top: 140px;
        font-size: 32px;
        color: $color-blackModeText;
    }
    &-not-subtitle{
        margin-top: 10px;
        font-size: 26px;
        color: $color-blackModeText;
    }
}
