@import "../scss/variables";

.dsm-c-rp-pagination {
  display: flex;
  height: 32px;

  .button {
    color: $color-blackModeText;
    border: 1px solid $color-blackModeDark;
    border-radius: 2px;
    text-align: center;
    height: 100%;
    width: 32px;
    line-height: 32px;
    margin-right: $space-8;
    box-sizing: border-box;

    &:hover {
      background-color: $color-blackModeUnder;
    }

    &:last-child {
      margin-right: 0;
    }

    & > .next-button {
      .dsm-c-rp-icon {
        transform: rotate(180deg);
      }
    }

    &.pagination-number {
      &.selected {
        background-color: $color-sysBlue;
        &:hover {
          background-color: $color-sysBlue;
        }
      }
    }

    &.hidden-number {
      border: 0;
      pointer-events: none;
    }

    &.custom-next-button {
      width: fit-content !important;
      white-space: nowrap;
      padding: 0 $space-8;
    }
  }
}
