@import '../../../scss/variables';

.c-check-select {
  .select-item-box {
    position: relative;
    height: 30px;
    line-height: 30px;

    .select-avatar {
      display: inline-block;
      margin-left: 10px;
      width: 20px;
      height: 20px;

      img {
        vertical-align: super;
      }
    }

    .name {
      display: inline-block;
      margin-left: 8px;
      max-width: calc(100% - 58px);
      white-space: pre;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .item-remove {
      display: inline-block;
      position: absolute;
      right: 12px;
      opacity: 0;

      i {
        position: relative;
        top: 2px;
        right: 0;
      }
    }

    &:hover {
      background-color: $color-greyBlueActive;

      .item-remove {
        opacity: 1;
      }
    }
  }
}