@import "~@dsm/scss/_variables";

.dsm-c-rp-color-plate {
  // flex: 1;
  width: 100%;
  height: 152px;
  position: relative;
  user-select: none;

  & > * {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;

    &:nth-child(2) {
      background: linear-gradient(to right, white, rgba(255, 255, 255, 0));
    }

    &:nth-child(3) {
      background: linear-gradient(to top, black, transparent);
    }

    &:last-child {
      pointer-events: all;
      width: 10px;
      height: 10px;
      border: 2px solid white;
      box-sizing: border-box;
      box-shadow: 0 0 2px 2px rgba(0,0,0,0.2);
      border-radius: 50%;
      transform: translate(-50%, -50%);
      user-select: none;
    }
  }
}