@import '../../../../scss/variables';

.rp-free-record {
  margin-top: 8px;

  .record-table {
    table-layout: fixed;
    width: 100%;

    thead {
      border-bottom: 1px solid $color-greyBlueBorder;

      td {
        line-height: 36px;
        padding-bottom: $space-4;
      }
    }

    tbody {
      td {
        line-height: 40px;

        &.reward {
          color: $color-sysRed;
        }
      }
    }

    td {
      text-align: center;
      height: 20px;
      font-size: 12px;
      color: #81868b;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      padding: 0 $space-4;
    }
  }

  .empty {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 48px;

    img {
      width: 160px;
      height: 72px;
    }

    &-tip {
      margin-top: $space-24;
      font-size: 12px;
      color: $color-grey;
      line-height: 20px;
    }
  }
}