@import '../scss/variables';

.dsm-c-rp-popover-container {
  position: relative;

  &:hover {
    & .dsm-c-rp-popover {
      display: block;
    }
  }

  .dsm-c-rp-popover {
    padding-left: 12px;
    position: absolute;
    left: 100%;
    top: -3px;
    display: none;
    width: 200px + 12px;

    .dsm-c-rp-popover-content {
      background-color: $color-white;
      padding: 10px 14px;
      box-sizing: border-box;
      line-height: 22px;
      color: $font-dark;
      box-shadow: 0px 1px 4px 0px rgba($color: $color-pureBlack, $alpha: .15);
      text-align: left;
      border-radius: 4px;
    }

    .dsm-c-rp-popover-arrow {
      position: absolute;
      top: 13px;
      left: 4px;
      overflow: hidden;
      width: 8px;
      height: 16px;
      z-index: 1;

      .dsm-c-rp-popover-arrow-content {
        display: inline-block;
        width: 10px;
        height: 10px;
        background-color: $color-white;
        transform: translate(3px) rotate(45deg);
        box-shadow: 0px 1px 4px 0px rgba($color: $color-pureBlack, $alpha: .15);
        border-radius: 1px;
      }
    }
  }
}