@import '../../../../scss/variables';

.c-tree-transfer {
  .select-item-box {
    position: relative;
    height: $space-30;
    line-height: $space-30;
    .select-avatar {
      display: inline-block;
      margin-left: $space-10;
      width: $space-20;
      height: $space-20;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        vertical-align: super;
      }
    }
    .name {
      display: inline-block;
      margin: 0 $space-8;
      white-space: pre;
      overflow: hidden;
      text-overflow: ellipsis;
      flex: 1;
    }
    .item-remove {
      display: none;
      margin-right: $space-12;

      i {
        position: relative;
        top: $space-2;
        right: 0;
      }
    }

    &:hover {
      background-color: $color-greyBlueActive;

      .item-remove {
        display: block;
      }
    }
  }
}
