@import '~@dsm/scss/_variables.scss';

.dsm-c-rp-input {
  height: 24px;
  line-height: 24px;
  max-width: 100%;
  width: 140px;
  position: relative;
  border-bottom: 1px solid transparent;
  display: flex;
  box-sizing: border-box;

  &.has-border {
    border-bottom: 1px solid $color-greyBlueActive;

    & > input[type='text'] {
      color: $color-blackModeUnder !important;
      white-space: pre-wrap !important;
      text-overflow: ellipsis !important;
      -webkit-user-drag: none;
      &:focus {
        white-space: normal;
        text-overflow: unset;
      }
    }
  }

  &.mac {
    & > input[type='text'] {
      transform: translateY(1px);
    }
    &.no-border,
    &.no-border-padding {
      & > input[type='text'] {
        transform: translateY(0);
      }
    }
  }

  &.no-border {
    border-bottom: none;

    &.focus {
      border-bottom: none;
    }
  }

  &.no-border-padding {
    border-bottom: none;
    padding: 0;
    &.focus {
      border-bottom: none;
    }
  }

  &.disabled {
    opacity: 0.3;
    user-select: none;
    pointer-events: none;
  }

  &:hover {
    border-color: var(--borderHoverColor);
    transition: all 0.5s;
  }
  &.focus {
    border-color: $color-sysBlue;
  }

  & > input[type='text'],
  & > input[type='password'] {
    padding: 0 0 1px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: $fs-M;
    width: 100%;
    outline: none;
    background: transparent;
    border: none;
    height: 100%;
    box-sizing: border-box;
    color: var(--textInputColor) !important;
    @include text-overflow;

    &:focus {
      border-color: $color-sysBlue;
      @include text-nooverflow;
    }

    &::selection {
      background: $color-sysBlue;
      color: $color-blackModeText;
    }

    &:read-only {
      user-select: none;
      border-bottom: 0;
    }

    &::-webkit-input-placeholder {
      color: var(--placeholder-color);
    }

    &::-moz-placeholder {
      color: var(--placeholder-color);
    }

    &:-moz-placeholder {
      color: var(--placeholder-color);
    }

    &:-ms-input-placeholder {
      color: var(--placeholder-color);
    }

    &::placeholder {
      color: var(--placeholder-color);
    }
  }

  &.dark {
    input {
      color: $color-darkest !important;
    }

    &:hover {
      border-color: $color-blackModeText !important;
    }

    &:focus {
      border-color: $color-sysBlue !important;
    }
  }

  .input-lenth {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
    color: #c0c4cc;
    background: #fff;
  }

  .input-suffix {
    color: var(--inputSuffix); //$color-blackModeAssistText;
    padding-left: 2px;
    flex-shrink: 0;
    flex-grow: 0;
  }

  .input-prefix {
    color: var(--inputSuffix); // $color-blackModeAssistText;
    padding-right: 2px;
    flex-grow: 0;
    flex-shrink: 0;
  }
}
