@import "~@dsm/scss/_variables";

.dsm-c-rp-color-general-hue {
  width: 100%;
  height: 10px;
  position: relative;
  // flex: initial;
  // margin-left: 10px;
  margin-bottom: 4px;
  user-select: none;
  cursor: pointer;
  background: linear-gradient(to right,
      #f00,
      #ff0,
      #0f0,
      #0ff,
      #00f,
      #f0f,
      #f00);

  &>.point {
    width: 10px;
    height: 10px;
    position: absolute;
    left: 0;
    top: 0px;
    box-sizing: border-box;
    border-radius: 50%;
    border: 2px solid white;
    background-color: transparent;
    transform: translateX(-50%);
    box-shadow: 0 0 2px 2px rgba(0,0,0,0.2);
    user-select: none;

    &:hover {
      opacity: .8;
    }
  }
}