@import '../../scss/variables';

.dsm-c-progress {
  height: 26px;
  position: relative;
  .track {
    width: 100%;
    height: 2px;
    background-color: $color-silverGrey;
    border-radius: 2px;
    position: absolute;
    bottom: 0;
    .progress {
      background-color: $color-sysRed;
      height: 100%;
      border-radius: 2px;
      position: absolute;
    }
  }
  .tips {
    position: absolute;
    bottom: 10px;
    width: 40px;
    margin-left: -20px;
    height: 20px;
    font-style: normal;
    font-size: $fs-S;
    color: $color-white;
    text-align: center;
    background-color: $color-sysRed;
    border-radius: 4px;
    user-select: none;
    &::after {
      content: '';
      position: absolute;
      bottom: -8px;
      left: 50%;
      margin-left: -4px;
      border: 4px solid transparent;
      border-top-color: $color-sysRed;
    }
  }
}
