@import '../../../scss/variables';

.c-tree-transfer-select-list {
  border-left: 1px solid transparent;
  flex: 1;
  .select-header {
    height: 36px;
    line-height: 36px;
    position: relative;
    color: $font-light;
    padding-left: 16px;

    .remove-all {
      position: absolute;
      right: 8px;
      cursor: pointer;

      &:hover {
        color: $color-sysRed;
      }
    }
  }
  .select-list {
    height: calc(100% - 36px);
  }
}
