@import '../../scss/variables';
@import '../../scss/z-index';
@import "../../scss/animation";

.dsm-c-comp-empty-dialog {
  .dsm-c-pop-wrap {
    position: fixed;
    z-index: 3000;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    cursor: default;

    &::before {
      display: inline-block;
      width: 0;
      height: 100%;
      vertical-align: middle;
      content: '';
    }
  }

  &.ie {
    .dsm-c-pop {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .dsm-c-bg {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 3000;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .3);
    animation: c-fade-in .1s ease-in 0s;


    &.closing {
      animation: c-fade-out .15s ease-out 0s;
    }
  }

  .dsm-c-pop {
    position: relative;
    z-index: 3001;
    width: 500px;
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    background-color: $color-white;
    box-shadow: 0px 12px 24px 0px rgba(0, 0, 0, .32);
    outline: 1px solid transparent;
    border-radius: $space-6;
    box-sizing: border-box;
    max-height: 90vh;
    animation: c-zoom-in-half .35s cubic-bezier(.5, 0, .5, 1.25) 0s;
    -webkit-font-smoothing: antialiased;
    transform-origin: center center;

    &.closing {
      animation: c-fade-out .15s ease-out 0s;
    }

    .dsm-c-pop-content {
      padding: 0;
      line-height: 22px;
    }

    .icon-_demo_close {
      position: absolute;
      top: $space-16;
      right: $space-16;
      cursor: pointer;
      font-size: 16px !important;
      z-index: 1;
      line-height: normal;
    }

  }
}