@import "~@dsm/scss/_variables";

.client-login-desktop-guest {
  width: 100%;
  height: 100%;
  background-color: $color-blackModeBackground;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  & > .container {
    width: 450px;
    padding: 0 64px;

    & > .content {
      &.hidden {
        display: none;
      }

      &.create-team {
        text-align: left;
        padding-bottom: 278px;
      }
    }

    .desktop-guest-img {
      width: 200px;
      height: 200px;
      margin: 0 auto;
    }

    .desktop-guest-title {
      line-height: 36px;
      font-size: 24px;
      font-weight: 700;
      margin-top: 17px;
      white-space: pre-wrap;
      color: $color-sysRed;
    }

    .desktop-guest-info {
      margin-top: 8px;
      line-height: 22px;
      font-size: 14px;
      color: $color-blackModeAssistText;
    }

    .desktop-guest-line {
      position: relative;
      margin-top: 32px;
      height: 22px;
      display: flex;
      align-items: center;
      justify-content: center;

      & .text {
        line-height: 22px;
        color: $color-blackModeAssistText;
        font-size: 14px;
        padding: 0 16px;
        background-color: $color-blackModeBackground;
        z-index: 1;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }

      & .line {
        width: 100%;
        border-bottom: 1px solid $color-blackModeDark;
      }
    }

    .desktop-guest-button {
      margin-top: 32px;
      height: 40px;
      line-height: 40px;
      background-color: $color-sysBlue;
      border-radius: 4px;
      text-align: center;
      cursor: pointer;

      &:hover {
        opacity: 0.8;
      }
    }

    .desktop-guest-switch {
      margin-top: 24px;
      line-height: 22px;
      color: $color-sysBlue;
      text-align: center;
      cursor: pointer;
    }

    .desktop-guest-input {
      margin-top: 24px;
      margin-bottom: 16px;
      width: 100%;
      line-height: 40px;
      height: 40px;
      border: 1px solid transparent;
      background-color: $color-blackModeUnder;
      border-radius: 4px;
      box-sizing: border-box;
      color: inherit;
      padding-left: 16px;

      // 
      &:focus,
      &.focus,
      &.focus:hover,
      &:hover {
        border: 1px solid $color-sysBlue;
        border-radius: 4px !important;
        outline: 0;
      }

      &.error {
        border-color: $color-sysRed;
      }
    }
  }
}
