@import '../../scss/variables';

.dsm-c-tag {
  padding: 0 $space-30;
  height: $space-40;
  line-height: $space-40;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &:hover {
    background-color: $color-silverGrey;

    .actions {
      visibility: visible;

      i {
        visibility: visible !important;
      }
    }
  }

  .actions {
    visibility: hidden;

    .action {
      margin-left: $space-20;
    }
  }
}

.no-tags-tip {
  color: $color-grey;
  padding: 20px 30px 20px 30px;
  display: inline-block;
}
