.dsm-c-rp-carousel-item {
  background: transparent no-repeat center center;
  background-size: cover;
  overflow: hidden;

  .dsm-c-rp-carousel-item-img {
    width: 100%;
    height: 100%;
    background-size: cover;
  }

  .dsm-c-rp-carousel-item-text {
    line-height: 22px;
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
  }
}
