@import '../../scss/variables';

.dsm-c-comp-dialog {
  .dsm-c-group-pop {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 0;
    position: fixed;
    z-index: 3001;
    width: 600px;
    background-color: $color-white;
    box-shadow: 0 15px $space-30 0 rgba(0, 0, 0, 0.15);
    border-radius: $space-6;
    box-sizing: border-box;
    max-height: 600px;
    height: 90%;
    animation: dialog-fade-in 0.3s ease-in-out;

    .icon-_demo_close {
      position: absolute;
      top: $space-16;
      right: $space-16;
      cursor: pointer;
      z-index: 1;
    }

    .group-manager-wrapper {
      width: 100%;

      .no-tags-tip {
        text-align: center;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        margin-top: 0;

        .no_group_img {
          width: 120px;
          height: 120px;
        }

        .no-group-text {
          margin-bottom: 16px;
        }
      }

      .dsm-c-panel-title {
        height: 132px;
        padding-left: $space-32;
        font-size: $fs-L;
        position: relative;
        padding-top: 24px;

        .title {
          font-weight: bold;
        }

        .add-group {
          margin-top: $space-32;
          font-size: $fs-M;
        }

        &-border {
          padding-left: 0px;
          margin: 0 $space-32;
          border-bottom: 1px solid $color-greyBlueActive;
        }
      }

      .dsm-c-tag {
        &:first-of-type {
          margin-top: 0px;
          border-top: 1px solid $color-greyBlueActive;
        }
      }

      .dsm-c-pop-title {
        white-space: normal;
      }
    }
  }
}