@import '../../scss/variables';

.c-empty-box {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  min-height: 600px;

  .empty-container {
    text-align: center;

    img {
      width: 120px;
      height: 120px;
    }

    .tip {
      font-size: 14px;
      font-weight: 400;
      color: $color-text;
      line-height: 22px;
      margin-top: $space-24;
    }
  }
}