@import '../scss/variables';

.dsm-c-rp-zoomer {
  font-size: $fs-M;
  position: relative;
  display: inline-block;

  & > .zoomer-content {
    display: flex;
    align-items: center;
    .zoom-btn {
      text-align: center;
      flex: initial;
      cursor: pointer;
      box-sizing: border-box;
      & > * {
        & > * {
          margin: 0 !important;
        }
      }

      & > .min-icon {
        .dsm-c-rp-icon {
          color: $color-green !important;
        }
      }

      & > .max-icon {
        .dsm-c-rp-icon {
          color: $color-yellow !important;
        }
      }

      & > .min-icon,
      & > .max-icon {
        .dsm-c-rp-icon {
          opacity: 1;
          &:hover {
            color: $color-sysBlue !important;
          }
        }
      }
    }

    &.disableL {
      .zoom-btn:first-child {
        pointer-events: none;
        opacity: 0.2;
      }
    }

    &.disableR {
      .zoom-btn:last-child {
        pointer-events: none;
        opacity: 0.2;
      }
    }

    .zoomer-con {
      margin: 0 3px;
      display: inline-block;
      width: 45px;
      text-align: center;
      position: relative;
      color: var(--textLabelColor); //$color-blackModeAssistText;

      & > .dsm-c-rp-input {
        position: absolute;
        left: 0;
        top: 0;
        height: 18px;
        & > input {
          text-align: center;
          line-height: 18px;
        }
      }
      &.editing {
        color: transparent;
        display: flex;

        input {
          text-align: center;
        }
      }

      &.disabled {
        pointer-events: none;
        opacity: 0.2;
      }

      &.mac {
        transform: translate(-3px, 0);
      }

      &:not(.editing)::after {
        content: '%';
      }
    }

    .menu-item::after {
      content: '';
    }
    & > .dsm-c-rp-drop {
      min-width: 100%;
      margin-top: $space-6;
      span {
        text-align: left;
        margin: 0;
        padding: 0 $space-20;
      }
    }
  }
}

.zoom-drop-down-list {
  //background-color: $color-blackModeDark;
  //color: $color-blackModeText;
  box-shadow: 0 4px 12px rgba($color-darkest, 0.15);
  .item {
    &.enabled:hover {
      //background: $color-darkest;
    }
  }
  &.light-drop {
    background-color: $color-white !important;
    border: none;
    color: $font-dark;
    .item {
      &.enabled:hover {
        background-color: $color-greyBlueActive;
      }
    }
  }
}
