@import '../../../../scss/variables';

.c-table-tr {

  &.hover {
    background-color: $color-backgroundGrey;
  }

  td {
    border-width: 1px 0 1px 0;
    border-style: solid;
    border-color: rgba($color: $color-greyBlueBorder, $alpha: .5);
    padding: 0 $space-24;
    height: $space-56;

    &.selection {
      padding: 0;
      // cursor: pointer;
      user-select: none;
    }

    &.operate {
      .dsm-c-icon {
        border-radius: $space-2;

        &:hover {
          background-color: $color-greyBlueActive;
        }
      }
    }

    &.c-table-column-first {
      padding-left: 0;
    }
  }
}