@import 'src/assets/clientLogin/scss/utils';
@import '~@dsm/scss/_variables';

.client-login-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;

  .logo {
    margin-top: 80px;
    @include bg2x('logo', 100px, 100px);
  }

  .logo-title {
    font-size: 20px;
    font-weight: 700;
    margin-top: 4px;
  }

  .logo-subtitle {
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 5px;
    margin-top: 10px;
  }

  .margin-t-52 {
    margin-top: 52px;
  }

  .margin-t-32 {
    margin-top: 32px;
  }

  .margin-t-24 {
    margin-top: 24px;
  }

  .btn,
  .dsm-c-rp-button.btn {
    box-sizing: border-box;
    width: 322px !important;
    width: 100%;
    height: 40px;
    background-color: $color-sysBlue;
    border-radius: 4px;
    line-height: 40px;
    text-align: center;
    font-size: 13px;
    cursor: pointer;
    user-select: none;
    color: $color-blackModeText;

    &:hover {
      opacity: 0.8;
    }
  }

  .btn-register {
    text-align: center;
    width: 322px;
    height: 40px;
    line-height: 40px;
    box-sizing: border-box;
    font-size: 13px;
    letter-spacing: 1px;
    border: 1px solid $color-blackModeDark;
    border-radius: 4px;
    cursor: pointer;
    user-select: none;
    color: $color-blackModeText;
    text-decoration-line: none;

    &:hover,
    &:focus {
      background-color: $color-blackModeUnder;
    }
  }

  .btn-switch {
    user-select: none;
    color: $color-blackModeText;
    font-size: 13px;
    height: 22px;
    line-height: 22px;
    cursor: pointer;
    text-decoration-line: none;
    letter-spacing: 1px;

    &:hover {
      color: $color-sysBlue;
    }
  }

  .enterprise-input-wrap {
    .enterprise-input {
      width: 322px;
      display: flex;
      align-items: center;
      background-color: #283142;
      height: 40px;
      box-sizing: border-box;
      border: 1px solid transparent;
      border-radius: 4px;
      padding: 0 16px;

      &:hover {
        border-color: $color-sysBlue;
      }

      &.focused {
        border-color: $color-sysBlue;
      }

      &.error {
        border-color: $color-sysRed;
      }

      > .dsm-c-rp-input {
        flex: 1 1 auto;
        border-radius: 4px;

        > input {
          font-size: 13px;
          color: $color-blackModeText !important;

          &::placeholder {
            color: $color-blackModeAssistText !important;
          }
        }
      }

      > .suffix {
        display: block;
        width: 16px;
        height: 16px;
        color: $color-blackModeAssistText;
      }
    }

    .enterprise-error-tip {
      color: $color-sysRed;
      font-size: 12px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      & > span {
        display: none;
      }
      &.show > span {
        display: block;
      }
    }
  }
}
.client-login-tip-dialog {
  transform: translateY(72px);
  .client-login-tip-dialog-content {
    padding-bottom: 32px;
  }
  .dsm-c-rp-dialog-content {
    background-color: $color-blackModeDark !important;
  }
  .close-button .dsm-c-rp-icon {
    color: $color-blackModeAssistText !important;
    &:hover {
      color: $color-blackModeAssistText !important;
      opacity: 0.8;
    }
  }
  .clientLogin-tips-format {
    @include bg2x('domain-format', 276px, 54px);
  }
  .clientLogin-tips-text {
    font-size: 13px;
    color: $color-greyText;
    margin-top: 16px;
    > span {
      color: $color-white;
    }
  }
}
