@import '../../scss/variables';

.dsm-c-panel-with-dialog {
  // background-color: $color-greyBlueBg;
  background-color: $color-white;
  max-width: 900px;
  min-width: 650px;
  margin: auto;
  box-sizing: border-box;
  border-radius: 6px;

  &-en {
    min-width: 720px;
  }

  .dsm-c-panel-title {
    width: 100%;
    height: 70px;
    line-height: 70px;
    border-bottom: 1px solid $color-lightGrey;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .panel-title-left {
      line-height: initial;
      display: flex;
      align-items: center;
      height: 70px;

      .panel-title-back-container {
        display: inline-flex;
        align-items: center;

        .back-link {
          outline: 0;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          text-decoration: none;

          .dsm-c-icon {
            width: 20px;
            height: 20px;
            margin-right: 6px;

          }

          &:hover {
            .dsm-c-icon {
              color: $color-sysRed;
            }
          }
        }

        &.backOnTitle {
          &:hover {

            .dsm-c-icon,
            .back-title {
              color: $color-sysRed;
            }
          }
        }
      }
    }


    h5 {
      display: inline-block;
      font-size: $fs-X;
      font-weight: normal;
    }

    .title-name {
      font-size: $fs-X;
      margin-right: $space-10;
    }

    .edit-title {
      float: left;
      display: flex;
      align-items: center;
      height: 100%;
      cursor: pointer;

      &:hover {
        .title-name {
          color: $color-sysRed;
        }

        .dsm-c-icon {
          color: $color-sysRed;
        }
      }

      .dsm-c-icon {
        line-height: initial;
      }
    }

    .actions {
      float: right;
      color: $color-sysRed;
      white-space: nowrap;

      span {
        margin-left: $space-20;
        cursor: pointer;

        &:first-of-type {
          margin-left: 0;
        }

        &.disabled {
          cursor: default;
          color: $color-grey;
          text-decoration: none;
        }
      }
    }
  }

  .top-operation {
    position: relative;

    .dsm-c-drop {
      right: -$space-20;
      top: $space-20;
    }
  }

  .dsm-c-panel-content {
    padding: 30px 30px;
  }

  .use-props-menu-title {
    position: relative;
  }

  #c-team-box {
    .dsm-c-comp-dialog {
      line-height: normal;

      .dsm-c-pop-title {
        color: $font-dark;
      }

      .icon-_demo_close {
        color: $icon-medium;

        &:hover {
          color: $color-sysRed;
        }
      }
    }
  }
}