@import '../../scss/variables';

.dsm-c-option-button,
.tag-button-group-setting {
  display: inline-block;
  position: relative;
  padding: 0 20px;
  margin: 0 10px 10px 0;
  line-height: 30px;
  border-radius: 100px;
  background-color: $color-silverGrey;
  transition: all .2s ease;
  cursor: pointer;
  user-select: none;
  vertical-align: middle;

  // i {
  //   position: absolute;
  //   top: 0;
  //   right: 2px;
  //   width: 16px;
  //   height: 16px;
