  //   color: $color-white;
  //   text-align: center;
  //   border-radius: 50%;
  //   background-color: $color-text;
  //   opacity: 0;
  //   cursor: pointer;
  //   user-select: none;
  //   transition: all .3s ease;
  //   &:hover {
  //     background-color: $color-sysRed;
  //   }
  // }

  &:hover i {
    top: -8px;
    opacity: 1;
  }

  &.option-selected {
    background-color: $color-darkGray;
    cursor: default;

    .tag-text,
    .dsm-c-icon {
      color: $color-white;
      cursor: default;
    }

    .tag-img {
      margin-left: 10px;
      cursor: pointer;
      vertical-align: middle;
    }
  }
}