@import "../../scss/variables";

.dsm-c-rp-carousel-dot {
  width: 8px;
  height: 8px;
  border: 1px solid transparent;
  background-color: rgba($color: #ffffff, $alpha: 0.5);
  margin: 2px;
  border-radius: 50%;
  overflow: hidden;

  &:hover {
    background-color: rgba($color: #ffffff, $alpha: 0.8);
  }

  &.changed {
    border-color: #bfe6ff;
    background-color: $color-sysBlue;
  }
}
