@import '../../scss/variables';

.dsm-help-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-size: 16px 16px;
  cursor: pointer;
  font-style: normal;
  color: $color-yellow;
  user-select: none;
  opacity: 1;
  margin: 0 5px;

  &:hover {
    color: $color-yellow;
  }

  &.dsm-c-icon-orea {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.dsm-c-icon-disabled {
  cursor: default;
  opacity: .3;

  &:hover {
    color: $icon-medium;
  }
}

.dsm-c-icon-solid-disabled {
  cursor: default;
  opacity: .35;

  &:hover {
    color: $color-iconGrey;
  }
}

.dsm-c-icon-solid-disableHover {
  cursor: default;

  &:hover {
    color: $icon-medium;
  }
}

.dsm-c-icon-solid-disableHoverColor {
  cursor: pointer;

  &:hover {
    color: $icon-medium;
  }
}

.tree-black-mode,
.content-black-model,
.single-header-black-model {
  .dsm-c-icon {
    color: $color-blackModeAssistText;

    &:hover {
      color: $color-blackModeText;
    }
  }

  .dsm-c-icon-solid-disableHover:hover,
  .dsm-c-icon-solid-disableHoverColor:hover,
  .dsm-c-icon-disabled:hover,
  .dsm-c-icon-solid-disabled:hover {
    color: $color-blackModeAssistText;
    pointer-events: none;
  }

  .icon-_demo_close {
    color: $icon-medium;

    &:hover {
      color: $color-sysRed;
    }
  }

  .c-note-panel {
    .dsm-c-icon {
      color: $icon-medium;

      &:hover {
        color: $color-sysRed;
      }
    }

    .dsm-c-icon-solid-disableHover:hover,
    .dsm-c-icon-solid-disableHoverColor:hover,
    .dsm-c-icon-solid-disabled:hover {
      color: $icon-medium;
    }
  }
}