.c-table-column {
  position: relative;

  .c-table-td-value {
    display: inline-block;
    overflow: hidden;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .compare {
    position: absolute;
    visibility: hidden;
    max-width: 100%;
    left: 0;
    top: 0;
    -webkit-transform: translate(-100%, -100%);
    transform: translate(-100%, -100%);
  }
}