@import "~@dsm/scss/_variables";

.dsm-c-rp-color-general-gradient {
  height: 30px;
  width: 100%;
  margin-top: -10px;
  user-select: none;
  display: flex;
  justify-content: space-between;
  align-items: center;

  & > .gradient-revert {
    margin-left: 10px;
  }

  & > .gradient-value {
    height: 10px;
    width: 160px;
    position: relative;

    & > .point {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;

      & > .point-item {
        position: absolute;
        width: 10px;
        height: 10px;
        top: 0;
        transform: translateX(-50%);
        box-sizing: border-box;
        user-select: none;
        border: 2px solid white;
        border-radius: 50%;
        box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.3);
        z-index: 2;
        background-color: white;

        &.selected {
          z-index: 1000;
        }

        & > * {
          pointer-events: none;
        }
      }
    }

    & > .gradient-content {
      height: 10px;
      width: 100%;
      position: relative;
      box-sizing: border-box;
      & > .gradient-background {
        height: 100%;
        width: 100%;
        bottom: 0;
        left: 0;
        position: absolute;
        background: url("../../../assets/image/transparent.png") repeat;
        background-size: 10px 10px;
      }

      & > .gradient-color {
        height: 100%;
        width: 100%;
        bottom: 0;
        left: 0;
        position: absolute;
      }
    }
  }

  & > .gradient-reset {
    position: absolute;
    right: 0;
    top: -6px;
  }
}
