@import '../scss/variables';

.dsm-c-rp-textarea {
  font-size: $fs-M;
  border: 1px solid $color-greyBlueActive;
  box-sizing: border-box;
  .container {
    height: fit-content;
    position: relative;
    .textarea {
      outline: none;
      margin: 0;
      padding: 4px;
      white-space: break-spaces; // pre-wrapbreak-spaces
      overflow-wrap: break-word;
      word-wrap: break-word;
      font-family: inherit;
      line-height: 1.15;
      font-size: $fs-M;
      border: none;
      box-sizing: border-box;
      resize: none;
      position: absolute;
      top: 0;
      bottom: 0;
      width: 100%;
      overflow: hidden;
      background-color: transparent;
      min-height: 100%;
    }
    .hidden {
      box-sizing: border-box;
      visibility: hidden;
      margin: 0;
      padding: 4px;
      white-space: pre-wrap;
      overflow-wrap: break-word;
      word-wrap: break-word;
      font-family: inherit;
      line-height: 1.15;
      font-size: $fs-M;
    }
  }
  &.disabled {
    opacity: 0.3;
    pointer-events: none;
    user-select: none;
  }
  &.focus,
  &:hover {
    border-color: $color-sysBlue;
  }
}
