@import '../../scss/variables';
@import '../../scss/theme';

.dsm-c-select {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  width: fit-content;
  height: $space-36;
  line-height: 1;
  user-select: none;
  cursor: pointer;

  &.blue {
    &:hover {

      .dsm-c-tooltip>.dsm-c-select-title,
      .icon-_tag_downarrow {
        color: $color-deepBlue;
      }
    }
  }

  &.full-border {
    border: $space-1 solid $color-greyBlueBorder;
    border-radius: $space-4;
    background: $color-white;

    .dsm-c-select-title {
      padding: 0 $space-16;
    }

    .dsm-c-icon {
      margin-right: $space-8;
    }

    &.position-left {
      .dsm-c-drop {
        right: 0;
      }
    }

    &.position-right {
      .dsm-c-drop {
        left: 0;
        right: 0;
      }
    }
  }

  &.position-left {
    .dsm-c-drop {
      left: 0;
    }
  }

  &.position-right {
    .dsm-c-drop {
      right: 0;
    }
  }

  &.search-mode {
    .dsm-c-select-title {
      border-bottom: $space-1 solid transparent;

      &:focus {
        border-color: $color-sysBlue !important;
      }
    }

    &:hover .dsm-c-select-title {
      border-bottom: $space-1 solid $color-mediumGrey;
    }

    &.search-mode-only-submit {
      .dsm-c-select-title {
        border: none;
      }
    }
  }

  .dsm-c-tooltip {
    display: flex;
    align-items: center;
    flex: $num-1;
    cursor: pointer;
    max-width: 100%;
  }

  &:hover .dsm-c-select-title {
    border-color: $color-sysBlue;
  }

  /* .dsm-c-select .dsm-c-select-title */
  .dsm-c-select-title {
    display: inline-block;
    // align-items: center;
    text-overflow: ellipsis;
    white-space: pre;
    overflow: hidden;
    line-height: $space-30+$space-5;
    height: 100%;
    max-width: 100%;
    padding-right: $space-4;
    border: 0;
    border-bottom: 1px solid $color-mediumGrey;
    background-color: rgba($color-white, 0);
    outline: 0;

    &::-webkit-input-placeholder {
      color: $font-input-placeholder;
    }

    &::-moz-placeholder {
      color: $font-input-placeholder;
    }

    &:-ms-input-placeholder {
      color: $font-input-placeholder;
    }

    &:focus {
      border-color: $color-sysBlue;
    }

    &.disable-edit {
      border-bottom: none;
      line-height: $space-36;
    }

    &.disable-unit {
      width: calc(100% - 35px);
    }

    &.input-focus {
      border-bottom: $space-1 solid $color-mediumGrey;
    }

    &.submit-search {
      width: 100%;
      border: none;
    }
  }


  .dsm-c-drop {
    min-width: 100px;

    .menu-text {
      padding-right: 0;
    }

    .menu-item {
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: pre;
      padding: 0 $space-20;
    }
  }

  .select-unit {
    line-height: $space-36;
    background-color: $color-white;
    margin: 0 $space-8;

    &.hide {
      margin: 0;
    }
  }

  .dsm-c-select-menu {
    position: absolute;
    left: 0;
    right: 0;
    top: calc(100% + 4px);


    &.direction-up {
      top: 0;
      bottom: 100%;

      .dsm-c-drop {
        bottom: 100%;
      }
    }
  }


  .needBorder {
    .dsm-c-drop {
      border: $space-1 solid;
    }
  }

  &:hover>.dsm-c-select-title,
  &:hover>.icon-_tag_downarrow,
  &:hover>.dsm-c-tooltip>.dsm-c-select-title,
  &:hover>.dsm-c-tooltip>.dsm-c-select-title .dsm-c-icon {
    color: $color-sysRed;
  }

  &:hover>.input-title {
    color: $font-dark;
  }
}

.dsm-c-select {

  &:hover>.dsm-c-select-title,
  &:hover>.icon-_tag_downarrow,
  &:hover>.dsm-c-tooltip>.dsm-c-select-title {
    @include theme(light) {
      color: $color-sysRed !important;
    }

    @include theme(dark) {
      color: $color-white !important;
    }
  }
}

.dsm-c-select.disabled-select {

  &:hover>.dsm-c-select-title,
  &:hover>.icon-_tag_downarrow,
  &:hover>.dsm-c-tooltip>.dsm-c-select-title {
    color: $color-text;

    &.idoc_iconfont {
      color: $color-blackModeAssistText !important;
    }
  }

  &:hover>.dsm-c-tooltip>.dsm-c-select-title .dsm-c-icon {
    cursor: default;
    color: $color-text;
  }
}

.dsm-c-select.disabled-select {
  user-select: none !important;
  cursor: default;

  &:hover {
    .dsm-c-tooltip {
      cursor: default;
    }

    .dsm-c-select-title {
      @include theme(light) {
        border-color: $color-textGrey !important;
        color: $color-text !important;
      }

      @include theme(dark) {
        border-color: $color-textGrey !important;
        color: $color-blackModeAssistText !important;
      }
    }

    .dsm-c-icon.icon-_tag_downarrow {
      color: $color-darkGray !important;
    }
  }
}

.disabled-select-unit,
.disabled-select-input,
.disabled-select-value {
  opacity: 0.2;
  user-select: none !important;
}