@import "../scss/variables";

.dsm-c-rp-icon-tooltip {
  display: inline-block;
  user-select: none;
  box-sizing: border-box;
  font-size: 16px;
  outline: none;
}

.dsm-c-rp-icon {
  border: 1px solid transparent;
  background: transparent;
  border-radius: 3px;
  box-sizing: border-box;
  color: var(--iconDefaultColor);
  display: inline-block;
  margin: unset !important;
  position: relative;

  &.img-icon {
    position: relative;
    background: transparent;
    &.selected {
      color: $color-blackModeText;
    }

    &:hover,
    &:active {
      color: $color-blackModeText;
    }

    &.disabled {
      opacity: 0.3;
      pointer-events: none;
    }

    & > img {
      margin: 4px;
    }
  }

  &.font-icon {
    font-weight: normal;
    @include medium-animation;
    i {
      z-index: 1;
      position: relative;
    }
    &.disabled {
      opacity: 0.3;
      cursor: default !important;
      pointer-events: none;
    }
    &.button-theme {
      cursor: pointer;
      &.selected {
        opacity: 1;
        color: var(--iconSelectedColor);
        &:hover {
          color: $color-sysBlue;
        }
      }
      &:active,
      &:hover {
        color: var(--iconHoverColor);
      }
    }

    &.hot-button-theme {
      .icon-background {
        opacity: 0;
        position: absolute;
        z-index: auto;
        top: 50%;
        left: 50%;
        width: 160%;
        height: 160%;
        transform: translate(-50%, -50%);
        background: $color-blackModeBackground;
        @include medium-animation;
      }

      &:active,
      &:hover {
        .icon-background {
          opacity: 1;
        }
      }
    }
  }

  .icon-background,
  label,
  img,
  i {
    pointer-events: none;
  }
}
