@import '../../scss/variables';

.dsm-c-search-input {
  height: 36px;
  position: relative;

  input {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    color: $color-text;
    border: 0;
    border-bottom: 1px solid $font-input-placeholder;
    outline: none;
    padding: 0 28px;
    transition: border-color .2s;

    &::-webkit-input-placeholder {
      color: $font-input-placeholder;
    }

    &::-moz-placeholder {
      color: $font-input-placeholder;
    }

    &:-ms-input-placeholder {
      color: $font-input-placeholder;
    }

    &:hover {
      border-color: $color-grey;
    }

    &:focus {
      border-color: $color-sysBlue;
    }
  }

  .dsm-c-disabled {
    cursor: default;
    color: $color-mediumGrey;
    background-color: inherit;
    pointer-events: none;

    &:focus {
      border-color: $color-textGrey !important;
    }
  }

  .dsm-c-icon {
    position: absolute;
    line-height: initial;
    top: 10px;
  }

  .icon-_search {
    left: 4px;
    cursor: default;
  }

  .icon-_close {
    right: 4px;
  }

  &-full-border {
    input {
      border: 1px solid $font-input-placeholder;
      border-radius: 4px;
      padding-left: 36px;
    }

    .icon-_search {
      left: 12px;
    }

    .icon-_close {
      right: 8px;
    }
  }

  &-inline-box {
    width: 100%;

    input {
      border-bottom: 1px solid $color-greyBlueBorder;
      padding: 0 36px;
    }

    .icon-_search {
      left: 12px;
    }

    .icon-_close {
      right: 10px;
    }
  }
}