@import '../../scss/variables';

.dsm-c-tag {
  margin: 0 $space-32;
  padding: 0px;
  height: $space-50;
  line-height: $space-50;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid $color-greyBlueActive;

  &:hover {
    background-color: $color-white;
    color: $color-sysRed;

    .actions {
      display: block;
    }
  }

  .actions {
    display: none;

    .action {
      margin-left: $space-20;
    }
  }

  &:first-of-type {
    margin-top: 18px;
  }
}

.no-tags-tip {
  color: $color-grey;
  padding: 20px 30px 20px 30px;
  display: inline-block;
}
