@import '../scss/variables';

.dsm-c-tree {
  background-color: var(--bgColorWrap);
  box-sizing: border-box;
  outline: none;
  white-space: nowrap;
  padding: 4px 0;

  input::selection,
  input::-moz-selection {
    color: #ffffff !important;
    background: $color-sysBlue !important;
  }

  .search-empty {
    text-align: center;
    width: 100%;
    padding: 30px 0;

    img {
      display: block;
      width: 60px;
      margin: 0 auto;
    }

    p {
      font-size: 14px;
      color: $font-light;
      opacity: 0.5;
      margin-top: 16px;
    }
  }

  &.max-width-setted {
    overflow: hidden;

    ul,
    ul ul,
    .title {
      max-width: 100%;
    }

    .title {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
  }

  ul {
    padding: 0;
    margin: 0;
    min-width: 100%;
    display: inline-block;
    box-sizing: border-box;
    list-style: none;

    li {
      line-height: 1em;
      margin: 0;
      padding: 0 0 0 16px;
    }
  }

  .tree-node {
    color: var(--treeTextColor);

    &.highlight {
      .node-bg-checked {
        background-color: #009dff;
        opacity: 0.15;
      }
    }

    .tree-node-name {
      color: var(--treeTextColor);
      i {
        color: var(--iconDefaultColor);
      }
      .name {
        color: inherit;
      }
    }

    .tree-node-name:hover {
      color: var(--treeTextColorHover);
      cursor: pointer;
      i {
        color: inherit;
      }
      .name {
        color: inherit;
      }
    }

    // .tree-node-name.hover {
    //   i.icon-expand:hover i {
    //     color: var(--treeTextColor) !important;
    //   }

    //   &:hover {
    //     cursor: pointer;

    //     .name {
    //       color: $font-dark !important;
    //     }

    //     i {
    //       color: $font-dark;
    //     }
    //   }
    // }

    .tree-node-input {
      color: var(--treeInputColor); 
      border-color: var(--treeInputBorder);
    }

    .searchKey-highlight {
      color: var(--treeTextColorHover);
    }
  }

  .node-bg-selected {
    background-color: var(--treeBgSelected) !important;
    opacity: 1 !important;
  }

  .node-bg-hover {
    background-color: $color-greyBlueActive !important;
    opacity: 0.5 !important;
  }

  .node-bg-drop-over {
    background-color: $color-sysBlue !important;
    opacity: 0.2 !important;
  }

  .selected {
    .name {
      color: var(--treeSelectedTextColor);
      i {
        color: var(--iconDefaultColor);
      }
    }
    &:focus {
      .name {
        color: var(--treeSelectedTextColor);
      }

      .title {
        i {
          color: var(--iconDefaultColor);
        }
      }
    }
  }

  &.is-finding-interaction-target {
    .tree-node .tree-node-name:hover {
      .is-finding-interaction-target-node {
        border-color: $color-sysRed;
        display: block;
        z-index: 1;
      }
    }
  }
