@import '../../../../../scss/variables';

@keyframes invitation-progress-active {
  0% {
    width: 0;
    opacity: 0.1;
  }

  20% {
    width: 0;
    opacity: 0.5;
  }

  100% {
    width: 100%;
    opacity: 0;
  }
}

.invitation-progress-container {
  margin-top: 16px;

  .header {
    margin-bottom: 41px;
  }

  .progress-container {
    .progress-bar {
      height: 32px;
      background: #ffd6ce;
      border-radius: 16px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;

      .inner {
        position: absolute;
        height: 100%;
        background: #08b156;
        width: 48px;
        border-radius: 16px;

        &::before {
          content: '';
          display: inline-block;
          width: 0;
          height: 100%;
          background: $color-white;
          border-radius: 16px;
          opacity: 0;
          animation: invitation-progress-active 2.4s cubic-bezier(.075, .82, .165, 1) infinite;
        }
      }

      .progress-node {
        min-width: 48px;
        position: relative;
        height: 32px;
        display: flex;
        align-items: center;

        &>div {
          margin: auto;
        }

        &-normal {
          width: 8px;
          height: 8px;
          background: #ff6f84;
          border-radius: 50%;
          transform: rotate(360deg);
        }

        &-active {
          width: 6px;
          height: 12px;
          border: 2px solid $color-white;
          border-width: 0 2px 2px 0;
          transform: rotate(45deg) translateY(-1px);
        }

        &-last {
          width: 26px;
          height: 26px;
          border-radius: 50%;
          transform: rotate(360deg);

          img {
            margin-top: -17px;
          }
        }

        &-desc {
          position: absolute;
          width: 80px;
          min-width: 48px;
          max-width: 80px;
          text-align: center;
          font-size: 12px;
          top: calc(100% + 9px);
          transform: translateX(-50%);
          left: 50%;
          white-space: nowrap;

          & > p {
            margin: 0;
            color: $color-grey;
          }

          & > p:first-of-type {
            color: $color-text;
            font-weight: 400;
          }

          &.active p {
            color: #08B156;
          }
        }
      }

    }

  }
}