@import '../../../../scss/variables';

.dsm-c-pagination-item {
  min-width: 32px;
  text-align: center;
  list-style: none;
  background-color: $color-white;
  border: 1px solid $color-greyBlueBorder;
  border-radius: 2px;
  outline: 0;
  cursor: pointer;
  user-select: none;
  font-size: 12px;
  margin-right: $space-8;

  a {
    display: block;
    padding: 0 $space-6;
    transition: none;
    color: $color-text;

    &:hover {
      text-decoration: none;
    }
  }
}

.dsm-c-pagination {
  &.red {
    .dsm-c-pagination-item {

      &-active,
      &:hover,
      &:focus {
        a {
          color: $color-sysRed;
        }

        border-color: $color-sysRed;
      }
    }
  }

  &.blue {
    .dsm-c-pagination-item {

      &-active,
      &:hover,
      &:focus {
        a {
          color: $color-deepBlue;
        }

        border-color: $color-deepBlue;
      }
    }
  }
}