@import '../../scss/variables';

.dsm-c-rp-preference-color-container {
  margin-top: 12px;
}

.dsm-c-rp-preference-color {
  width: 100%;
  display: grid;
  gap: 8px;
  padding-right: 12px;
  box-sizing: border-box;
  grid-template-columns: repeat(8, 1fr);

  .dsm-c-rp-preference-color-item {
    width: 20px;
    height: 20px;
    border: var(--pickerValueRecentBorder); //1px solid $color-blackModeBorder;
    box-sizing: border-box;
    overflow: hidden;
    svg {
      pointer-events: none;
    }
    &.blink {
      animation: blink 0.3s ease-in-out;
    }
  }

  & > .dsm-c-rp-icon-tooltip {
    width: 20px;
  }
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
