@import '../../scss/variables';

.dsm-c-scrollview {
  position: relative;

  &-box::-webkit-scrollbar {
    width: 0;
    height: 0;
  }

  &.disabled {
    &>* {
      &:nth-child(1) {
        overflow: hidden !important;
      }
    }

    &:hover {
      .track-bar {
        opacity: 0 !important;
      }
    }
  }

  &>* {
    z-index: 1;

    &:nth-child(1) {
      overscroll-behavior: contain;
      z-index: 0;
    }
  }

  &:hover {
    .track-bar {
      opacity: 1 !important;
    }
  }

  .track-bar {
    &>div {
      background-color: rgba($color-black, .2) !important;
      border-radius: 10px !important;

      &:hover {
        background-color: rgba($color-black, .4) !important;
      }
    }
  }

  .h-track {
    height: 6px !important;
    width: 100%;
    bottom: 0;
    left: 0;
    -webkit-transform-style: preserve-3d;
  }

  .v-track {
    width: 6px !important;
    height: 100%;
    top: 0;
    right: 0;
    -webkit-transform-style: preserve-3d;
  }
}