@import '../scss/variables';

/* theme1 */
.dsm-c-rp-button-group {
  display: flex;
  flex-wrap: nowrap;
  justify-content: start;
  & > .dsm-c-rp-icon-button {
    border: none !important;
    background: $color-blackModeDark;
    // border: {
    //   top: 1px solid $color-blackModeDark;
    //   bottom: 1px solid $color-blackModeDark;
    // };
    border-radius: 0;

    &:first-child {
      // border-left: 1px solid $color-blackModeDark;
      border-top-left-radius: 3px;
      border-bottom-left-radius: 3px;
    }

    &:last-child {
      // border-right: 1px solid $color-blackModeDark;
      border-top-right-radius: 3px;
      border-bottom-right-radius: 3px;
    }
  }

  & > .dsm-c-rp-button {
    border-radius: 0;

    &:not(:last-child) {
      border-right: none;
    }

    &:first-child {
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
    }

    &:last-child {
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }
  }

  & > .dsm-c-rp-icon-button.noborder {
    // background: $color-blackModeUnder;

    & + .dsm-c-rp-icon-button.noborder {
      border-left: none !important;
    }

    &:hover {
      background-color: var(--deviceOriHover);
    }

    &.actived {
      background-color: $color-sysBlue;

      .btn-text {
        color: $color-blackModeText !important;
      }

      .dsm-c-rp-icon {
        &.selected {
          i {
            color: $color-blackModeText !important;
          }
        }
      }

      &:disabled {
        .font-icon i {
          color: $color-blackModeText !important;
        }
      }
    }
  }

  &.light {
    & > .dsm-c-rp-icon-button {
      border-left-color: $color-sysBlue;
      border-right-color: $color-sysBlue;
      border-top-color: $color-sysBlue;
      border-bottom-color: $color-sysBlue;
      background-color: $color-white;
      & > span {
        color: $font-dark;
      }
    }
  }
}
