@import '~@dsm/scss/_variables.scss';

.tree-node-rp {
  box-sizing: border-box;
  position: relative;
  padding: 2px 0;

  .node-bg {
    height: $space-32;
    position: absolute;
    right: 0;
    top: 0;
  }

  .tree-node-input {
    //width: 100%;
    height: $space-32;
    position: relative;
    padding: 0 $space-8;
    border: none;
    border-bottom: 1px solid $color-blue;
    outline: none;
    background: none;
    box-sizing: border-box;
    user-select: auto;
  }

  .tree-node-name {
    display: flex;
    height: $space-32;
    line-height: $space-32;
    position: relative;
    outline: none;
    box-sizing: border-box;
    align-items: center;
    width: 100%;

    .dsm-c-checkbox {
      height: 32px;
    }

    .is-finding-interaction-target-node {
      height: $space-32;
      position: absolute;
      right: 0;
      top: -2px;
      border: 2px solid transparent;
      display: none;
    }

    .icon-expand {
      &.invisiable {
        visibility: hidden;
      }

      line-height: normal;
      height: inherit;
      display: flex;
      align-items: center;
    }

    .right-icon {
      float: right;
      box-sizing: border-box;
      line-height: normal;
      margin: auto $space-8;
    }

    .suffix-icon {
      margin-left: $space-8;
    }

    .options-icon {
      margin-left: $space-8;
    }

    .title {
      border: 1px solid transparent;
      flex: 1;
      height: 100%;
      overflow: hidden;
      line-height: $space-30;
      cursor: pointer;

      i {
        margin-right: 5px;
        vertical-align: text-bottom;
        line-height: 16px;
      }

      .name {
        position: relative;
        word-break: break-all;
        white-space: pre-wrap;
        line-height: 32px;
        height: 32px;
        margin-top: 1px;

        .group-num {
          opacity: 0.7;
          font-weight: normal;
        }
      }

      &:hover {
        color: $color-sysBlue;
        i {
          color: $color-sysBlue;
        }
      }
    }

    &.dullness {
      .title {
        color: $icon-medium;
      }
    }

    &.lowlight {
      .title {
        opacity: 0.5;
      }
    }
  }

  &.tree-node-is-dragging {
    opacity: 0.5;

    .tree-node-is-dragging-bg {
      position: absolute;
      min-width: calc(100% + 2px);
      height: calc(100% + 2px);
      top: 0;
      right: -1px;
      bottom: -2px;
      z-index: 4;
    }
  }

  &.tree-node-drop-at {
    &-before,
    &-after {
      &:before {
        display: inline-block;
        content: '';
        width: 100%;
        height: 2px;
        background-color: $color-blue;
        position: absolute;
        left: 0;
      }

      &:after {
        display: inline-block;
        content: '';
        width: $space-8;
        height: $space-8;
        border-radius: $space-8;
        background-color: $color-blue;
        position: absolute;
        left: 0;
      }
    }

    &-before {
      &:before {
        top: -1px;
      }

      &:after {
        top: -4px;
      }
    }

    &-after {
      &:before {
        bottom: -1px;
      }

      &:after {
        bottom: -4px;
      }
    }

    &-child {
      & > .tree-node-name {
        .title {
          border: 1px solid $color-blue;
          z-index: 1;
        }

        &:after {
          display: inline-block;
          content: '';
          width: $space-8;
          height: $space-8;
          border-radius: $space-8;
          background-color: $color-blue;
          position: absolute;
          left: $space-32;
          bottom: -5px;
          z-index: 1;
        }
      }
    }
  }

  &.tree-node-cutted {
    opacity: 0.5;
  }

  & > .has-interaction {
    width: 0;
    height: 0;
    position: absolute;
    border-width: 3px;
    border-style: solid;
    border-color: $color-sysBlue transparent transparent $color-sysBlue;
    margin: 0;
  }

  & > .isInteractionTarget {
    height: $space-32;
    position: absolute;
    right: 0;
    top: -2px;
    border: 2px solid $color-sysBlue;
    background: rgba($color-sysBlue, 0.4);
  }

  & > .interaction_target_node {
    height: $space-32;
    position: absolute;
    right: 0;
    top: -2px;
    background: transparent;
    pointer-events: none;
  }

  & > .interaction-highlight {
    height: $space-30;
    position: absolute;
    right: 0;
    top: 0;
    border: 2px solid $color-sysBlue;
    pointer-events: none;
  }

  .checkbox-viewer-inner {
    width: 11px !important;
    height: 11px !important;
  }
}

.tree-node-display {
  cursor: not-allowed !important;

  i {
    color: $color-greyBlueBorder !important;
    &:hover {
      color: $color-greyBlueBorder;
      cursor: not-allowed;
      .name {
        color: $color-greyBlueBorder;
      }
    }
  }

  .name {
    color: $color-greyBlueBorder !important;
  }

  .tree-node-name {
    cursor: not-allowed !important;
  }

  .title {
    cursor: not-allowed !important;

    &:hover {
      color: $color-greyBlueBorder;

      i {
        color: $color-greyBlueBorder;
        cursor: not-allowed;
      }

      .name {
        cursor: not-allowed;
      }
    }
  }
}
