//  mixin

@mixin text-overflow() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@mixin text-nooverflow() {
  overflow: initial;
  text-overflow: initial;
  white-space: initial;
}

@mixin slow-animation() {
  transition-duration: 0.25s;
  transition-timing-function: ease-in;
}

@mixin fast-animation() {
  transition-duration: 0.1s;
  transition-timing-function: ease-in;
}

@mixin medium-animation() {
  transition-duration: 0.15s;
  transition-timing-function: ease-in;
}

@mixin small-drop-shadow() {
  box-shadow: 0 1px 4px rgba($color-pureBlack, 0.16);
}

@mixin comp-default-shadow() {
  box-shadow: 0 3px 6px rgba($color-pureBlack, 0.15);
}

@mixin medium-drop-shadow() {
  box-shadow: 0 4px 12px rgba($color-darkest, 0.1);
}

@mixin large-drop-shadow() {
  box-shadow: 0 5px 30px rgba($color-darkest, 0.15);
}

@mixin dialog-drop-shadow() {
  box-shadow: 0 15px 30px rgba($color-darkest, 0.15);
}

@mixin background-mask() {
  background-color: rgba($color-darkest, 0.3);
}

@mixin disabled() {
  opacity: 0.3;
  pointer-events: none;
  cursor: default;
}

@mixin scrollbar() {
  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-button {
    background-color: transparent;
    height: 0;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: transparent;
    border-radius: 11px;
  }

  &:hover {
    &::-webkit-scrollbar-thumb {
      background-color: rgba($color-blackModeAssistText, 0.3);
      &:hover {
        background-color: rgba($color-blackModeAssistText, 0.6);
      }
    }
  }
}
