@import '../scss/variables';
@import '../scss/zIndex';

@keyframes show-pop {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.dsm-c-rp-dialog {
  position: fixed;
  z-index: $dialog-index;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;
  font-size: $fs-M;
  user-select: none;

  display: flex;
  justify-content: center;
  align-items: center;

  transition-property: background-color;
  transition-duration: 0.5s;
  background-color: transparent;
  color: $color-darkest;

  &.fade {
    background-color: rgba($color-darkest, 0.15);
  }

  &.top {
    display: block;

    & > .dsm-c-rp-dialog-content {
      margin: auto;
      top: $space-10 * 20;
    }
  }

  & > .dsm-c-rp-dialog-content {
    position: relative;
    min-height: 100px;
    max-height: 100%;
    border-radius: 6px;
    background: $color-white;
    display: flex;
    animation: show-pop 0.15s cubic-bezier(0.34, 0.34, 0.5, 1.25) 0s;
    box-shadow: 0 15px 30px rgba($color-darkest, 0.15);
    overflow: hidden;

    &.transparent {
      background-color: transparent;
    }

    &.fade-out {
      animation: fade-out 0.15s ease-in;
    }

    & > .dialog-panel-content {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;

      & > .title {
        position: relative;
        z-index: 0;
        flex: initial;
        display: flex;
        height: 46px;
        // box-sizing: border-box;
        font-size: $fs-XL;
        font-weight: 600;
        color: $font-dark;
        user-select: none;

        & > .title-value {
          line-height: 22px;
          margin: 0 32px;
          pointer-events: none;
          align-self: flex-end;
        }

        &.hidden {
          height: $space-8 * 4;
        }
        // 
        &.title-without-separator {
          padding-bottom: 24px;
        }
        &.title-with-separator {
          padding-bottom: 16px;
        }

        & > .separator {
          height: 0;
          position: absolute;
          left: 0;
          right: 0;
          bottom: $space-8;
          border-top: 1px solid $color-blackModeText;
          pointer-events: none;
          display: none;
          &.hidden {
            display: none;
          }
        }
      }

      & > .close-button {
        position: absolute;
        top: 15px;
        right: 14px;
        z-index: 2;
        color: $icon-medium;

        .dsm-c-rp-icon {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 16px !important;
          height: 16px !important;
        }

        &:hover {
          .dsm-c-rp-icon {
            color: $color-blackModeBorder;
          }
        }
      }

      & > .content {
        flex: 1;
        border: none;
        margin: 0 32px;
        // overflow: hidden;
        position: relative;
        line-height: 1.45;
        // &.fit {
        //margin-top: 50px;
        // }

        &.stick {
          margin: 0;
        }
      }
    }

    .dsm-c-rp-icon {
      color: $icon-medium;
      &:hover {
        color: $color-blackModeBorder;
      }
    }
    .dsm-c-rp-checkbox,
    .dsm-c-rp-label,
    .dsm-c-rp-select,
    .dsm-c-rp-button {
      color: $color-darkest;
    }

    .dsm-c-rp-checkbox {
      &:disabled {
        opacity: 0.3;
      }
    }

    .dsm-c-rp-button {
      &.dsm-c-rp-button-dialog {
        &.activated {
          color: $color-sysBlue;
        }
        &:hover {
          background-color: #f8f6f6;
          opacity: 1;
        }
        &:active {
          background-color: #f8f6f6;
          color: $font-dark;
          opacity: 1;
        }
      }

      &.dsm-c-rp-button-only-text.activated {
        color: $color-sysBlue !important;
      }
    }

    .dsm-c-rp-button {
      &.dsm-c-rp-button-only-text.activated.idoc {
        color: $color-sysRed !important;
      }
    }

    .dsm-c-rp-number-input,
    .dsm-c-rp-input {
      input {
        color: $color-darkest !important;
      }

      border-bottom: 1px solid $color-greyBlueBorder;

      &:hover {
        border-bottom: 1px solid $color-greyBlueBorder !important;
      }

      &.focus {
        border-color: $color-sysBlue;
        &:hover {
          border-color: $color-sysBlue !important;
        }
      }

      &.no-border {
        border: 0 !important;
      }

      input[type=text],
      input[type=password]{
        &::-webkit-input-placeholder {
            color: $color-greyText;
          }
        
          &::-moz-placeholder {
            color: $color-greyText;
          }
        
          &:-moz-placeholder {
            color: $color-greyText;
          }
        
          &:-ms-input-placeholder {
            color: $color-greyText;
          }
        
          &::placeholder {
            color: $color-greyText;
          }
      }
    }
  }

  // 
  .footer-without-separator {
    padding: 24px 0;
    margin-right: -8px;
  }
  .footer-with-separator {
    padding: 16px 24px 24px 0;
  }
  .footer-without-separator,
  .footer-with-separator {
    &.right-footer {
      display: flex;
      justify-content: flex-end;
    }

    .dsm-c-rp-button {
      margin-right: 10px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}
