@import '../../scss/variables';

/* theme1 */
.dsm-c-button {
  box-sizing: border-box;
  min-width: 100px;
  height: 36px;
  color: $color-white;
  border: 0;
  background-color: $color-sysRed;
  outline: 0;
  cursor: pointer;
  margin: 0;
  padding: 0;
  border-radius: 4px;

  &:hover {
    color: $color-white;
    background-color: $color-sysRed;
    opacity: 0.8;
  }

  &:active {
    color: $color-white;
    background-color: $color-sysRed;
  }

  &:disabled {
    color: rgba($color-white, 0.5);
    background-color: $color-sysRed;
    cursor: default;
    opacity: 0.3;
  }

  /* theme2 */
  &.dsm-c-button-border {
    color: $color-blackModeText;
    border: 1px solid $color-blackModeBorder;
    background-color: $color-white;

    &:hover {
      border: 1px solid $color-blackModeAssistText;
    }

    &:active {
      border: 1px solid $color-blackModeBorder;
    }

    &:disabled {
      border: 1px solid $color-blackModeDark;
      cursor: default;
      opacity: 0.5;
    }
  }

  /* theme3 */
  &.dsm-c-button-grey-border {
    color: $color-text;
    border: 1px solid $color-darkGray;
    background-color: $color-white;

    &:hover {
      background-color: $color-milkyWhite;
    }

    &:active {
      background-color: $color-silverGrey;
    }

    &:disabled {
      color: $color-lightGrey;
      border: 1px solid $color-lightGrey;
      background-color: $color-white;
      cursor: default;
    }
  }


  /* theme4 */
  &.dsm-c-button-icon {
    min-width: 140px;
    line-height: 34px;
    color: $color-text;
    border: 1px solid $color-darkGray;
    background-color: $color-white;

    &:hover {
      background-color: $color-milkyWhite;
    }

    &:active {
      background-color: $color-silverGrey;
    }

    &:disabled {
      color: $color-lightGrey;
      border: 1px solid $color-lightGrey;
      background-color: $color-white;
      cursor: default;

      .dsm-c-icon {
        color: $color-lightGrey;
      }
    }

    .dsm-c-icon {
      margin-right: 10px;

      &:hover {
        color: $color-text;
      }
    }
  }

  /* theme5 */
  &.dsm-c-button-small {
    width: 60px;
    height: 30px;
    line-height: 30px;
    min-width: 60px;

    &:hover {
      color: $color-white;
      background-color: $color-sysRed;
      opacity: 0.8;
    }

    &:active {
      color: $color-white;
      background-color: $color-sysRed;
    }

    &:disabled {
      color: rgba($color-white, 0.5);
      background-color: $color-sysRed;
      cursor: default;
      opacity: 0.3;
    }
  }

  /* theme5 */
  &.dsm-c-button-blue-border {

    color: $color-deepBlue;
    border: 1px solid $color-deepBlue;
    background-color: $color-white;

    &:hover {
      background-color: $color-milkyWhite;
    }

    &:active {
      background-color: $color-silverGrey;
    }
  }

  &.white {
    color: $color-sysRed;
    border: 1px solid $color-sysRed;
    background-color: $color-white;

    &:hover {
      color: #ff5476;
      border: 1px solid #ff5476;
      background-color: $color-milkyWhite;
    }

    &:active {
      color: #ff365e;
      border: 1px solid #ff365e;
      background-color: $color-milkyWhite;
    }

    &:disabled {
      color: #feaab8;
      border: 1px solid #feaab8;
      background-color: $color-white;
      cursor: default;
    }
  }

  &.blue {
    color: $color-white;
    background-color: $color-deepBlue;
    border: none;
    font-size: 13px;

    &:hover {
      opacity: 0.8;
    }

    &:disabled {
      opacity: .3;
    }
  }
}

/* play-btn */
.dsm-c-button-play {
  display: none;
  height: 36px;
  border: 1px solid #eaebec;
  border-radius: 13px;
  font-size: 1.4rem;
  color: #222;
  background: #eaebec;
  cursor: pointer;
  outline: none;
  position: absolute;
  transform: translateX(-50%);
  bottom: 20px;
}

.show-icon .dsm-c-button-play {
  display: unset;

  .dsm-c-icon {
    color: #222;
    padding: 0;
  }
}

.dsm-c-button-play:hover {
  color: #fe4066;

  .dsm-c-icon {
    transition: none;
    color: #fe4066;
  }
}

/* ds-sidebar-download */
.sidebar-download-btn {
  width: 88px !important;
  height: 30px;
  background: #fff;
  border: 1px solid #b8bbbf;
  color: #2e2f30;
  border-radius: 4px;
}

.sidebar-download-btn:hover {
  cursor: pointer;
  color: $color-sysRed;
  border-color: $color-sysRed;
}

.download-btn-disabled {
  opacity: 0.5;
}

.download-btn-disabled:hover {
  cursor: not-allowed;
  color: #2e2f30;
  border-color: #b8bbbf;
}

@media screen and (max-width: 450px) {
  .dsm-c-button {
    font-size: 12px;
  }
}