
// var 
$font-color:#eef0f4;
$color-primary:#409EFF;

@mixin devicePixelRatio($ratio:2) {
    @media screen and (min-resolution:#{$ratio+"x"}){
		  @content
    }
};

@mixin bg2x($url,$width,$height){
    width: $width;
    height: $height;
    background-image:url('~assets/clientLogin/images/'+$url+'.png') ;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    background-attachment: scroll;
    @include devicePixelRatio(){
        width: $width;
        height: $height;
        background-image:url('~assets/clientLogin/images/'+$url+'@2x.png') ;
    }
};