@import '../../scss/variables';
@import '../../scss/z-index';

.dsm-c-drop {
  position: absolute;
  min-width: 120px;
  margin: 0;
  padding: $space-10 0;
  background-color: $color-white;
  box-shadow: 0 $space-4 12px 0 rgba(0, 0, 0, .15);
  text-align: left;
  user-select: none;
  z-index: 2000;

  li {
    line-height: $space-30;
    text-overflow: ellipsis;
    // white-space: pre;
    white-space: nowrap;
    position: relative;


    &.has-children {
      &:hover {
        background-color: $color-silverGrey;

        .dsm-c-drop-child {
          display: block;
        }
      }

      .dsm-c-drop-child {
        position: absolute;
        top: -$space-10;
        display: none;

        &.left {
          right: 100%;
        }

        &.right {
          left: 100%;
        }
      }

      .children-arrow {
        float: right;
        line-height: 1;
        display: block;
        margin-top: 7px;
        margin-right: $space-8;
      }
    }

    &.has-icon {
      display: flex;

      &:hover {
        background-color: $color-silverGrey;
        cursor: pointer;
      }

      &>.dsm-c-icon {
        position: absolute;
        left: $space-4;
        margin-right: $space-10;
      }

      .menu-item {
        padding-left: $space-20+$space-5;
      }
    }

    .menu-item {
      display: inline-block;
      box-sizing: border-box;
      width: 100%;
      padding: 0 0 0 $space-20;
      text-decoration: none;
      color: $font-dark;

      &:hover {
        background-color: $color-silverGrey;
        cursor: pointer;
      }

      .dsm-upgrade-tooltip {
        margin: 0 !important;
        margin-left: 5px !important;
        // word-break: break-all;
      }

      .tips {
        display: inline-block;
        background-color: $color-sysRed;
        color: $color-white;
        font-style: normal;
        font-size: $fs-S;
        padding: 0 8px;
        margin-left: 10px;
        border-radius: 4px;
        width: auto !important;
        height: auto !important;
      }

      .tipText {
        font-style: normal;
        font-size: $fs-S;
        margin-left: 30px;
      }

      &-title {
        &.menu-item-title-badge {
          display: flex;
          align-items: center;
        }

        .badge {
          &-dot {
            height: 100%;
            position: relative;
            margin-left: 4px;

            .dot {

              display: inline-block;
              width: 5px;
              height: 5px;
              background: #fe4066;
              border-radius: 50%;
              position: absolute;
              top: 0;

            }
          }

          &-count {
            display: inline-block;
            margin-left: 4px;
            background: #fe4066;
            color: #fff;
            min-width: 16px;
            height: 16px;
            line-height: 16px;
            width: fit-content;
            text-align: center;
            border-radius: 10px;
            font-size: 10px;
          }
        }
      }

      &.suffix {
        padding: 0 $space-20;

        .menu-item-suffix {
          float: right;
          margin-left: $space-10;
        }
      }
    }

    .select-item {
      background-color: $color-silverGrey;
      cursor: pointer;
    }

    &.split-line {
      display: block;
      height: 1px;
      width: 100%;
      background-color: $color-mediumGrey;
      margin: $space-4 0;
    }

    &.disable {
      &:hover {
        background: none;
        cursor: default;
      }
    }
  }

  .dsm-c-disabled {
    cursor: unset !important;

    &>.menu-item-title,
    &>p,
    &>.menu-item-suffix {
      opacity: .25;
    }

    &:hover {
      background-color: $color-white !important;
      cursor: unset !important;
    }

    .dsm-upgrade-tooltip {
      font-size: 1.4rem !important;

      p {
        font-size: 1.4rem !important;
        color: #2e2f30 !important;
      }
    }
  }
}