@import '../../scss/variables';

.dsm-c-panel {
  background-color: $color-white;
  box-shadow: 0 3px 10px 0 rgba(0, 0, 0, .15);
  width: 90%;
  max-width: 960px;
  min-width: 650px;
  margin: auto;
  box-sizing: border-box;
  border-radius: 6px;

  .dsm-c-panel-title {
    width: 100%;
    padding: 0 30px;
    height: 70px;
    line-height: 70px;
    border-bottom: 1px solid $color-lightGrey;
    box-sizing: border-box;

    .panel-title-left {
      line-height: initial;
      display: flex;
      align-items: center;
      height: 70px;
    }

    .back-link {
      margin-right: 6px;
      outline: 0;
      width: 20px;
      height: 20px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      text-decoration: none;

      &:hover {
        .dsm-c-icon {
          color: $color-sysRed;
        }
      }
    }

    h5 {
      display: inline-block;
      font-size: $fs-X;
      font-weight: normal;
    }

    .edit-title {
      float: left;
      display: flex;
      align-items: center;
      height: 100%;
    }

    .actions {
      float: right;
      color: $color-sysRed;

      span {
        margin-left: 20px;
        cursor: pointer;
      }
    }
  }

  .dsm-c-panel-content {
    padding: 30px 30px;
  }
}