@import '../../scss/variables';
@import '../../scss/z-index';
@import "../../scss/animation";

.dsm-c-comp-dialog {
  .dsm-c-pop-wrap {
    position: fixed;
    z-index: 3000;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    cursor: default;

    &::before {
      display: inline-block;
      width: 0;
      height: 100%;
      vertical-align: middle;
      content: '';
    }
  }

  .dsm-c-scrollview {
    .track-bar {
      transition: none !important;
    }

    .h-track {
      animation: none !important;

      opacity: 0 !important;

      &:hover {
        opacity: 0 !important;
      }
    }
  }

  &.ie {
    .dsm-c-pop {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .dsm-c-bg {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 3000;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .3);
    animation: c-fade-in .1s ease-in 0s;


    &.closing {
      animation: c-fade-out .15s ease-out 0s;
    }
  }

  .dsm-c-pop {
    position: relative;
    z-index: 3001;
    width: 500px;
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    background-color: $color-white;
    box-shadow: 0 15px $space-30 0 rgba(0, 0, 0, .15);
    outline: 1px solid transparent;
    padding: $space-24 $space-32;
    border-radius: $space-6;
    box-sizing: border-box;
    max-height: 90vh;
    animation: c-zoom-in-half .35s cubic-bezier(.5, 0, .5, 1.25) 0s;
    // backface-visibility: hidden;
    -webkit-font-smoothing: antialiased;
    transform-origin: center center;

    &.closing {
      animation: c-fade-out .15s ease-out 0s;
    }

    &.empty-title {
      padding: $space-32 $space-32 $space-24;
    }

    .dsm-c-pop-title {
      margin-bottom: $space-24;
      overflow: hidden;
      text-overflow: ellipsis;
      max-width: 92%;
      font-size: 16px;
      font-weight: 600;
      line-height: 22px;

      .dsm-c-pop-sub-title {
        color: $font-light;
        font-size: $fs-M;
      }

      &-bold {
        font-size: $fs-L;
        color: $color-text;
      }

      &-light {
        margin-top: 8px;
        font-size: 14px;
        font-weight: 400;
        height: auto;
      }
    }

    .dsm-c-pop-content {
      padding: 0;
      line-height: 22px;
      margin: 0 auto $space-24;
    }

    .icon-_demo_close {
      position: absolute;
      top: $space-16;
      right: $space-16;
      cursor: pointer;
      font-size: 16px !important;
      z-index: 1;
      line-height: normal;
    }

    .dsm-c-button-wrap {
      text-align: right;

      .dsm-c-checkbox {
        top: $space-10;
        float: left;
      }

      button {
        min-width: 60px;
        height: 36px;
        border: 0;
        background-color: $color-white;
        font-weight: bold;
        border-radius: $space-4;
        outline: 0;
        padding: 0 $space-8;
        text-align: center;
        margin-left: $space-10;
        color: $font-dark;
        transition: all .2s;

        &:first-of-type {
          margin-left: 0;
        }

        &:active {
          color: $color-text;
          background-color: $color-white;
        }

        &:hover {
          background-color: $color-silverGrey;
          cursor: pointer;
        }
      }

      .dsm-c-spc-button {
        color: $color-sysRed;

      }

      .dsm-c-disable-button {
        color: $color-mediumGrey;

        &:hover {
          background-color: rgba($color-white, 0);
          cursor: unset;
        }
      }

    }
  }

  &.theme-blue {
    .dsm-c-pop {
      .icon-_demo_close {
        &:hover {
          color: $color-deepBlue
        }
      }

      .dsm-c-button-wrap {
        .dsm-c-spc-button {
          color: $color-deepBlue;
        }

        .dsm-c-disable-button {
          color: $color-mediumGrey;

          &:hover {
            background-color: rgba($color-white, 0);
            cursor: unset;
          }
        }
      }
    }
  }
}