@import '../../scss/variables';

.c-check-select {
  width: 380px;
  height: 286px;
  border: 1px solid $color-textGrey2;
  display: flex;
  font-size: 12px;

  .c-check-list {
    border-right: 1px solid $color-textGrey2;
    flex: 1;
    overflow: hidden;
    white-space: nowrap;
    height: 100%;
  }

  .c-select-list {
    border-left: 1px solid rgba(0, 0, 0, 0);
    flex: 1;
  }

  .checkbox-viewer {
    box-sizing: border-box;
  }
}
