@import "../scss/variables";

.dsm-c-rp-number-input {
  height: 24px;
  max-width: 100%;
  position: relative;
  box-sizing: border-box;
  line-height: 24px;
  border-bottom: 1px solid transparent;
  display: flex;

  &.disabled {
    opacity: .3;
    user-select: none;
    pointer-events: none;
  }

  &:hover {
    border-color: var(--borderHoverColor); 
    transition: all .5s;
  }

  &.focus {
    border-color: $color-sysBlue;
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type=number] {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    outline: none;
    background: transparent;
    color: var(--textInputColor);
    border: none;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 0;
    font-size: $fs-M;
    -moz-appearance: textfield;
    &:focus::placeholder {
      color: transparent;
      border-color: $color-sysBlue;
    }
    &:out-of-range,
    &:invalid,
    &:-moz-submit-invalid,
    &:-moz-ui-invalid {
      outline: 0;
      border-color: transparent;
      box-shadow: unset;
    }

    &.no-border {
      border-bottom: none;

      &:focus {
        border-bottom: none;
      }
    }

    &:read-only {
      user-select: none;
      border-bottom: 0;
    }

    &::-webkit-input-placeholder {
      padding-left: .8rem;
      color: $color-blackModeBorder;
    }

    &::-moz-placeholder {
      padding-left: .8rem;
      color: $color-blackModeBorder;
    }

    &:-moz-placeholder {
      padding-left: .8rem;
      color: $color-blackModeBorder;
    }

    &:-ms-input-placeholder {
      padding-left: .8rem;
      color: $color-blackModeBorder;
    }

    &::placeholder {
      padding-left: .8rem;
      color: $color-blackModeBorder;
    }

    &::selection {
      background: $color-sysBlue;
      color: $color-blackModeText;
    }

  }

  &.dark {
    input {
      color: $color-darkest !important;
    }

    &:hover {
      border-color: $color-blackModeText !important;
    }

    &:focus{
      border-color: $color-sysBlue !important;
    }
  }

  .input-suffix {
    padding-left: 2px;
    color: $color-blackModeAssistText;
    flex-shrink: 0;
    flex-grow: 0;

  }

  .input-prefix {
    padding-right: 2px;
    color: $color-blackModeAssistText;
    flex-grow: 0;
    flex-shrink: 0;
  }
}
