@import '../../scss/variables';

.dsm-c-textarea2 {
  width: 100%;

  textarea {
    width: 100%;
    resize: none;
    line-height: 1.5;
    color: $color-text;
    outline: none;
    padding: $space-8;
    outline: none;
    border: 1px solid $color-mediumGrey;
    &::-webkit-input-placeholder {
      color: $color-mediumGrey;
    }

    &::-moz-placeholder {
      color: $color-mediumGrey;
    }

    &:-ms-input-placeholder {
      color: $color-mediumGrey;
    }
    &:focus {
      border-color: $color-sysBlue;
    }
  }
}
