@import "../scss/variables";

.dsm-c-rp-switch {
  position: relative;
  width: 60px;
  height: 30px;
  display: inline-block;
  user-select: none;
  cursor: pointer;
  @include fast-animation;
  transition-property: background-color;
  & > i {
    position: absolute;
    top: 2px;
    background: var(--switchCtrlBg);
    width: 26px;
    height: 26px;
    @include fast-animation;
    transition-property: all;
  }

  &.on {
    background-color: var(--switchOnBg);
  }

  &.off {
    background-color: var(--switchOffBg);
    & > i {
      opacity: var(--switchCtrlOpacity);
    }
  }

  &.undefined {
    background-color: $icon-medium;
  }

  &.disabled {
    cursor: default;
    pointer-events: none;
    opacity: .3;
  }
}