@import '../../../../scss/variables';
.check-group {
  line-height: 30px;
  min-height: 30px;

  .checkbox-text,
  .check-name {
    display: inline-block;
    margin-left: 8px;
    max-width: 135px;
    font-size: 12px;
    white-space: pre;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: middle;
  }

  .check-group-box {
    line-height: 1;
    height: 30px;
    vertical-align: middle;
    position: relative;
    display: flex;
    align-items: center;

    .expand-container {
      display: inline-block;
      padding-left: 10px;
    }

    .group-title-wrapper {
      width: calc(100% - 53px);
      display: flex;
      align-items: center;

      .group-name {
        display: inline-block;
        max-width: calc(100% - 30px);
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }

      &>i {
        line-height: 1;
      }
    }

    .group-title-wrapper.can-add-group {
      .group-name {
        max-width: calc(100% - 72px);
      }
    }

    .check-group-add {
      position: absolute;
      right: 8px;
      opacity: 0;
    }

    &:hover .check-group-add {
      opacity: 1;
    }
  }

  .check-item .dsm-c-checkbox {
    padding-left: 38px;
  }

  .check-name,
  .dsm-c-checkbox {
    padding-left: 4px;
  }

  .reach-max-tip {
    position: fixed;
    top: calc(50% - 190px);
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 0 12px;
    box-shadow: 0 2px 2px 2px rgba(0, 0, 0, .1);
    border-radius: 2px;
    color: $color-sysRed;
    background: $color-white;

    i {
      top: 1px;
      margin-right: 8px;
    }
  }
}
