@import "../scss/_variables";

.dsm-c-rp-link {
  text-decoration: none;
  outline: 0;

  &:hover {
    text-decoration: underline;
  }

  &.dsm-c-rp-link-basic {
    color: $font-dark;

    &:hover {
      color: $color-sysRed;
    }
  }

  &.dsm-c-rp-link-red {
    color: $color-sysRed;
  }

  &.dsm-c-rp-link-dark {
    color: $color-sysBlue;
    &:hover {
      color: $color-sysBlue;
    }
  }
}

.link-disabled {
  opacity: .3;
  cursor: default;
  pointer-events: none;
}
