@import '../../scss/variables';

.dsm-c-slider {
  display: inline-block;
}

.dsm-c-slider-click {
  display: inline-block;
  height: 16px;
  position: absolute;
  left: 0;
  top: -6px;
}

.dsm-c-sliding-block {
  //width: 200px;
  height: 2px;
  background-color: $color-lightGrey;
  border-radius: 2px;
  position: relative;
  display: inline-block;
  vertical-align: middle;

  .dsm-c-block {
    height: 100%;
    background-color: $color-iconGrey;
    border-radius: 2px;
    position: relative;
    pointer-events: none;
  }

  .dsm-c-block-btn {
    display: inline-block;
    width: 12px;
    height: 12px;
    border: 1px solid $color-mediumGrey;
    background-color: $color-white;
    border-radius: 50%;
    box-shadow: 0 1px 1px rgba($color-black, .15);
    position: absolute;
    top: -5px;
    right: -7px;
    pointer-events: auto;
  }
}

.dsm-c-block-title {
  width: 30px;
  text-align: center;
  background-color: $color-white;
  font-size: 12px;
  display: inline-block;
  margin: 0 10px;
  //position: absolute;
  //top: -6px;
  //right: -40px;
  user-select: none;
  /* Non-prefixed version, currently not supported by any browser */
}

.disabled-slider-title {
  opacity: .25;
}

.disabled-slider-block {
  background-color: $color-lightGrey !important;
}