@import '../../../../scss/variables';

.dsm-c-pagination-size-changer {
  display: inline-block;
  margin-right: $space-8;

  .dsm-c-select {
    height: $space-32;
    transition: all .3s cubic-bezier(.645, .045, .355, 1);
    border-radius: $space-2;

    &-title {
      font-size: 14px;
      line-height: $space-30;
    }

    &.full-border {
      .dsm-c-select-title {
        padding-left: $space-12;
      }
    }

    .dsm-c-icon {

      &.icon-_tag_downarrow {
        color: $color-greyBlueBorder;
      }
    }

    &:hover {
      border-color: $color-sysRed;

      .dsm-c-tooltip>.dsm-c-select-title {
        color: $color-text;
      }
    }

    .direction-up .dsm-c-drop {
      min-width: 95px;
      bottom: calc(100% + 6px);

      .menu-item {
        padding: 0 $space-16;
      }

    }
  }

  &.red {
    .dsm-c-select:hover {
      border-color: $color-sysRed;
    }

    &:focus {
      outline: none;

      .dsm-c-select {
        border-color: $color-sysRed;
        box-shadow: 0 0 0 2px rgba(254, 64, 102, .2);
      }
    }
  }

  &.blue {
    .dsm-c-select:hover {
      border-color: $color-deepBlue;
    }

    &:focus {
      outline: none;

      .dsm-c-select {
        border-color: $color-deepBlue;
        box-shadow: 0 0 0 2px rgba(24, 144, 255, .2);
      }
    }
  }
}