@import "~@dsm/scss/_variables";

.dsm-c-rp-color-input {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  & > .color-hex {
    font-size: 13px;
  }
  & > .color-rgb, &> .color-alpha {
    display: flex;
    margin-left: $space-8;
    padding-top: 2px;

    .hidden-input {
      width: 0;
      height: 0;
      opacity: 0;
      pointer-events: none;
    }
  }
}