@import '../../../scss/variables';
@import '../../../scss/z-index';

.dsm-upgrade-tooltip {
  position: relative;
  display: inline-block;
  cursor: pointer;
  line-height: 1.15;
  padding: 5px 15px 5px 0;
  margin-left: 5px;
  word-break: break-word;

  .tips-content {
    position: absolute;
    white-space: normal;
    background-color: $color-white;
    line-height: 24px;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, .15);
    z-index: 1000;
    display: none;
    left: 40px;
    top: -20px;
    width: 300px;
    height: fit-content;
    padding: 8px 11px;
    text-align: left;
    border-radius: 5px;
    box-shadow: 1px 1px 8px 0px rgba(0, 0, 0, .16);

    &.right {
      right: 0;
      transform: translateX(0);
    }

    &:before {
      content: '';
      width: 0px;
      height: 0px;
      border-top: 10px solid transparent;
      border-bottom: 10px solid transparent;
      border-right: 10px solid rgba(0, 0, 0, .1);
      position: absolute;
      top: 20px;
      left: -11px;
    }

    &:after {
      content: '';
      width: 0px;
      height: 0px;
      border-top: 10px solid transparent;
      border-bottom: 10px solid transparent;
      border-right: 10px solid $color-white;
      position: absolute;
      top: 20px;
      left: -10px;
    }

    .go-buy-link {
      word-break: keep-all;
    }
  }

  &:hover {
    .tips-content {
      display: block;
      color: $color-text;
    }
  }
}