@import '../../../../../scss/variables';

@keyframes move {
  0% {
    top: 100%;
  }

  100% {
    top: -272px;
  }
}

.invitation-received-forever {
  margin-top: 48px;
  height: 102px;
  overflow: hidden;
  text-align: center;
  position: relative;
  z-index: 0;

  &::before,
  &::after {
    content: "";
    display: inline-block;
    position: absolute;
    left: 0;
    height: 1px;
    right: 0;
    z-index: 1;
    box-shadow: 0px 0 20px 20px $color-white;
  }

  &::before {
    top: -1px;
  }

  &::after {
    bottom: -1px;
  }

  ul {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    transition: all .5s linear;

    li {
      line-height: 34px;
      font-size: 12px;
      white-space: nowrap;

      .match-str-wrapper {
        color: $color-grey;
      }
    }
  }
}