@import '../../../scss/variables';

.rp-free-dialog-main {
  height: 100%;
  position: relative;
  overflow: hidden;
  border-radius: $space-20 $space-20 0 0;

  .main-bg {
    width: 632px;
    height: 320px;
    background: linear-gradient(180deg, #ff8e9f, #ff5c70);
    border-radius: 20px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .main-content {
    position: absolute;
    left: 20px;
    right: 20px;
    top: 0;
    bottom: 0;

    .main {
      background-color: $color-white;
      height: 100%;
      padding: $space-24 $space-32 0;
      border-radius: $space-20;
      position: relative;
      box-sizing: border-box;

      &::before,
      &::after {
        content: '';
        display: inline-block;
        width: 300px;
        height: 100px;
        position: absolute;
        bottom: -60px;
        z-index: 1;
      }

      &::before {
        background: linear-gradient(to bottom right, #fdb6c3, #f57b8c 50%);
        transform: rotate(15deg);
        left: -50px;
        box-shadow: 4px 0 10px 0px rgba(226, 66, 137, .61);
      }

      &::after {

        background: linear-gradient(to bottom left, #fdb6c3, #f57b8c 50%);
        transform: rotate(-15deg);
        right: -50px;
        box-shadow: -4px 0 10px 0px rgba(226, 66, 137, .61);
      }

      .back-wrapper {
        height: 26px;
        font-size: 16px;
        font-weight: 500;
        color: #696b6c;
        line-height: 26px;

        .back {
          cursor: pointer;

          &:hover {

            span,
            .dsm-c-icon {
              color: $color-sysRed;
            }
          }
        }
      }

      .button-wrapper {
        height: 80px;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        justify-content: center;

        .dsm-c-button {
          width: 144px;
          height: 40px;
          font-size: 14px;
          border-radius: 19px !important;
          box-shadow: 0px 3px 10px 0px rgba(226, 66, 137, 0.61);

          &.get-forever-btn {
            background: #FF9111 !important;
            box-shadow: 0px 3px 10px 0px rgba(226, 141, 64, .61);
          }

          &:disabled {
            opacity: 1;
            background: #969798;
            color: $color-white;
            box-shadow: none;
          }
        }
      }
    }
  }
}