.rp-free-receive-forever {
  display: flex;
  flex-direction: column;
  align-items: center;

  .qr-code {
    width: 210px;
    height: 210px;
    margin: 16px 0 16px;
    position: relative;

    .loading {
      position: absolute;
    }

    img {
      width: 100%;
    }
  }

  .desc-wrap {
    font-size: 14px;
    color: #2e2f30;
    line-height: 22px;
    margin-bottom: 16px;

    p {
      margin: 0;
    }

    .red {
      color: #fe4066
    }
  }

  .notes {
    font-size: 12px;
    line-height: 20px;
    color: #969798;

    .note {
      color: #fe4066;
      margin-right: 2px;
    }
  }
}