@import '../../scss/variables';
@import '../../scss/theme';

.dsm-c-checkbox {
  display: inline-block;
  position: relative;
  cursor: pointer;
  user-select: none;
  transition: opacity .15s ease-out;

  &.disabled-checkbox {
    opacity: .3;
    cursor: not-allowed;
    transition: opacity .15s ease-out;

    .checkbox-viewer {
      cursor: not-allowed;
    }
  }

  @include theme('dark') {

    &.dsm-c-checkbox-dark {
      .checkbox-viewer {
        background: $color-blackModeUnder;
        color: $color-blackModeText;
        border-color: $color-blackModeBorder;

        .checkbox-viewer-inner {
          border-color: $color-blackModeBorder;
          background-color: $color-blackModeUnder;
        }
      }

      .checkbox-viewer.checkbox-checked {
        position: relative;
        text-align: center;

        .checkbox-viewer-inner {
          border-color: $color-blackModeBorder;
          background-color: $color-blackModeUnder;

          &::after {
            opacity: 1;
            transform-origin: center;
            transform: rotate(45deg)  translate(-1.2px, -1px);
            transition: all .1s cubic-bezier(.12, .4, .29, 1.46) .1s;
          }
        }
      }
    }
  }

  .dsm-c-link {
    vertical-align: middle;
    margin-left: $space-6;
    line-height: 17px;
  }

  &:hover {
    .checkbox-viewer {
      .checkbox-viewer-inner {
        border-color: $color-darkGray;
      }
    }
  }

  @include theme('dark') {

    &.dsm-c-checkbox-dark:hover {
      .checkbox-viewer {
        .checkbox-viewer-inner {
          border-color: $color-blackModeBorder;
        }
      }
    }
  }

  .checkbox-viewer,
  .checkbox-text {
    vertical-align: middle;
  }

  .checkbox-viewer {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: middle;

    .checkbox-viewer-inner {
      position: relative;
      left: 0;
      top: 0;
      display: inline-block;
      width: 13px;
      height: 13px;
      border: 1px solid $color-textGrey2;
      background-color: $color-white;
      border-radius: 2px;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: background-color .2s ease-out, border-color .2s ease-out;

      &::after {
        content: '';
        position: absolute;
        width: 3px;
        height: 6px;
        border: 2px solid #fff;
        border-left: 0;
        border-top: 0;        
        box-sizing: content-box;
        // left: 2px;
        // top: 5px;
        // opacity: 0;
        
        transform: rotate(45deg) scale(0) translate(-1.2px, -1px);
        transform-origin: center;
        transition: all .1s cubic-bezier(.12, .4, .29, 1.46) .1s;
      }
    }

    &.checkbox-checked {
      position: relative;
      text-align: center;

      .checkbox-viewer-inner {
        border-color: $color-darkGray;
        background-color: $color-darkGray;
        transition: background-color .2s ease-out, border-color .2s ease-out;

        &::after {
          opacity: 1;
          transform: rotate(45deg) translate(-1.2px, -1px) ;
          transform-origin: center;
          transition: all .1s cubic-bezier(.12, .4, .29, 1.46) .1s;
        }
      }
    }

    &.checkbox-checked-indeterminate {
      .checkbox-viewer-inner::after {
        content: '';
        top: 2px;
        left: 2px;
        width: 7px;
        height: 7px;
        background-color: $color-darkGray;
        border: 0;
        transform: scale(1);
        opacity: 1;
      }
    }
  }

  .checkbox-text {
    margin-left: $space-10;
    font-size: 13px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    width: fit-content;
    display: inline-block;
    line-height: 17px;
  }

  .dsc {
    color: $color-greyText;
    font-size: $fs-S;
    margin-top: $space-8;
  }

  &.dsm-c-checkbox-auto-line {
    .checkbox-text {
      height: 18px;
      white-space: pre-wrap;
      overflow: visible;
      text-overflow: unset;
      line-height: 18px;
    }
  }
}