@import '../scss/variables';

.dsm-c-rp-radio {
  display: inline-flex;
  width: fit-content;
  justify-content: center;
  align-items: center;
  margin: 6px;
  line-height: 22px;
  height: 22px;
  user-select: none;
  cursor: default;

  &.c-radio-disabled {
    opacity: 0.3;
  }

  $viewer-size: 14px;
  $center-size: 6px;

  .radio-viewer {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: $viewer-size;
    height: $viewer-size;
    border-radius: 50%;
    border: 1px solid var(--radioDefaultBorderColor);
    box-sizing: border-box;
  }

  .radio-checked:after {
    content: '';
    display: block;
    width: $center-size;
    height: $center-size;
    border-radius: 50%;
    background-color: var(--radioSelectedDotColor);
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }

  .radio-text {
    margin-left: 10px;
    font-size: 14px;
    color: var(--textColor); // $color-white;
  }

  // &.them-light {
  //   .radio-viewer {
  //     border-color: $color-blackModeBorder2;
  //   }

  //   .radio-text {
  //     color: $color-white;
  //   }
  //   .radio-checked::after {
  //     background-color: $color-blackModeText;
  //   }
  // }

  &.them-dark {
    .radio-viewer {
      border-color: $icon-medium;
      background: transparent;
    }

    .radio-text {
      color: $font-dark;
    }
    .radio-checked::after {
      background-color: $icon-medium;
    }
  }
}
