@import '~@dsm/scss/_variables';
@import '~@dsm/scss/_zIndex';

.dsm-c-rp-color-panel {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: $menu-index;
  pointer-events: none;

  .dsm-c-rp-select {
    color: $color-blackModeText;
  }

  & > .dsm-c-rp-color-panel-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: transparent;
    user-select: none;
  }

  & > .dsm-c-rp-color-panel-content {
    border: 1px solid var(--borderColorBreakMain);
    pointer-events: all;
    width: 236px;
    position: absolute;
    background: var(--bgColorWrap);
    color: $color-blackModeAssistText;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
    padding: 0 0 12px 12px;
    border-radius: 4px;
    outline: none;
    user-select: none;

    .dsm-c-rp-color-panel-content-special {
      padding-right: 12px;
    }

    .dsm-c-rp-color-content {
      border-bottom: 1px solid var(--borderColorBreakMain);

      & > .dsm-c-rp-color-panel-type {
        padding: $space-10 0;
        display: flex;
        justify-content: space-between;

        .dsm-c-rp-select > .select-value {
          flex: unset;
        }

        .dsm-c-rp-color-source-model {
          & > *:last-child {
            margin-left: $space-8;
          }
        }
      }

      & > .dsm-c-rp-color-panel-general {
        margin-bottom: 10px;
        .dsm-c-rp-color-panel-general-change {
          margin: 8px 0;
          height: 24px;
          & > div {
            vertical-align: middle;
          }
          .dsm-c-rp-color-panel-general-changeOn {
            display: inline-block;
            width: 160px;
            margin: 0 10px 0 12px;
          }
          .dsm-c-rp-color-panel-general-afterChange {
            display: inline-block;
            width: 24px;
            height: 24px;
            background-color: $color-blackModeUnder;
            border: 1px solid var(--borderColorBreakMain); //$color-blackModeDark;
            box-sizing: border-box;
            background: url('../../../assets/image/transparent.png') repeat;
            background-size: 10px 10px;
            & > div {
              width: 100%;
              height: 100%;
            }
          }
          .dsm-c-rp-color-panel-general-collector {
            display: inline-block;
            width: 18px;
          }
        }
      }

      & > .dsm-c-rp-color-panel-edit-input {
        margin-top: 14px;
        margin-bottom: 10px;
        .inner-block {
          & > label {
            font-size: 12px;
            color: $color-greyText;
            display: inline-block;
            margin-top: 4px;
          }
        }
      }
    }
  }

  .dsm-c-rp-color-default-use {
    margin: 5px 0;
    border-bottom: 1px solid $color-blackModeDark;

    .preset {
      margin-bottom: 5px;
    }

    &.no-border {
      border-bottom: 0;
    }
  }

  .dsm-c-rp-color-before-use {
    border-bottom: 1px solid var(--borderColorBreakMain);
    &.no-list {
      display: none;
    }
  }

  &.in-popup {
    z-index: $dialog-menu-index;
  }

  &.light {
    & .input-suffix,
    & .input-prefix {
      color: $color-greyText;
    }

    & > .dsm-c-rp-color-panel-content {
      background: $color-white;
      border: 1px solid $color-blackModeText;
      label {
        color: $color-darkest;
      }
      .dsm-c-rp-input,
      .dsm-c-rp-number-input {
        &:hover {
          border-bottom: 1px solid $color-blackModeText !important;
        }
      }
      .dsm-c-rp-number-input input[type='number'],
      .dsm-c-rp-input input[type='text'] {
        color: $color-darkest !important;
      }

      .dsm-c-rp-icon {
        color: $icon-medium;
        &:hover {
          color: $color-blackModeBorder;
        }
        &.selected {
          color: $color-sysBlue;
        }
      }

      & .dsm-c-rp-color-before-use,
      & .dsm-c-rp-color-content,
      & .dsm-c-rp-color-default-use {
        border-bottom: 1px solid $color-lightGray;
      }

      .dsm-c-rp-color-panel-general-afterChange,
      .dsm-c-rp-preference-color-item,
      .favorite-item,
      .recent-item,
      .preset-item {
        border: 1px solid $color-blackModeText !important;
      }
      .favorite-add {
        .dsm-c-rp-icon {
          color: $icon-medium;
        }
      }
      .favorite-item {
        & > .del {
          border: 0;
        }
      }
    }
  }
}
