@import '../scss/_variables';

.dsm-c-rp-progressbar {
  height: 32px;
  position: relative;
  width: calc(100% - 40px);
  margin: 0 auto;

  &.red {
    .progress-track > .progress-track-rate {
      background-color: $color-sysRed;
    }
    .progress-tips {
      background-color: $color-sysRed;
      &:after {
        border-top-color: $color-sysRed;
      }
    }
  }
  &.blue {
    .progress-track > .progress-track-rate {
      background-color: $color-sysBlue;
    }
    .progress-tips {
      background-color: $color-sysBlue;
      &:after {
        border-top-color: $color-sysBlue;
      }
    }
  }

  & > .progress-track {
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #f0f0f0;
    border-radius: 2px;
    bottom: 0;
    & > .progress-track-rate {
      height: 100%;
      border-radius: 2px;
      position: absolute;
      transition: 500ms;
    }
  }
  & > .progress-tips {
    position: absolute;
    bottom: 12px;
    width: $space-40;
    margin-left: -$space-20;
    height: $space-20;
    font-style: normal;
    font-size: $fs-S;
    color: #fff;
    line-height: $space-20;
    text-align: center;
    border-radius: $space-4;
    user-select: none;
    transition: 500ms;
    &:after {
      content: '';
      position: absolute;
      bottom: -8px;
      left: 50%;
      margin-left: -$space-4;
      border: $space-4 solid transparent;
    }
  }
}
