@import "../../scss/variables";
.dsm-c-rp-color-recent-colors{
  display: flex;
  flex-wrap: wrap;
  margin: 0 -4px;
  padding: 0 4px;
  .recent-item{
    width: $space-20;
    height: $space-20;
    box-sizing: border-box;
    background: url("../../../assets/image/transparent.png") repeat;
    background-size: 10px 10px;
    border: var(--pickerValueRecentBorder);
    cursor: pointer;

    >.color-content{
      width: 100%;
      height: 100%;
    }
  }

  &>*{
    margin: 12px 0 12px 9px;
    &:nth-child(8n + 1) {
      margin-left: 0;
    }
  
    &:nth-child(8n) {
      margin-right: 0;
    }
  }
}