@import '../../scss/variables';

.dsm-c-input {
  width: 140px;
  height: 36px;
  position: relative;
  display: inline-block;

  input {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    font-size: 14px;
    color: $color-text;
    border: 0;
    border-bottom: 1px solid $color-mediumGrey;
    outline: none;
    padding: 0 4px;
    transition: border-color .2s;

    &::-webkit-input-placeholder {
      color: $color-mediumGrey;
    }

    &::-moz-placeholder {
      color: $color-mediumGrey;
    }

    &:-ms-input-placeholder {
      color: $color-mediumGrey;
    }

    &:hover {
      border-color: $color-grey;
    }

    &:focus {
      border-color: $color-sysBlue;
    }
  }

  &.dsm-c-input-error {

    input,
    span {
      color: $color-sysRed;
    }

    input {
      border-color: $color-sysBlue;
    }

    span {
      position: absolute;
      top: 46px;
      left: 4px;
    }

    .dsm-c-input-error-msg {
      font-size: $fs-12;
      line-height: 22px;
    }
  }

  &-full-border {
    input {
      border: 1px solid $font-input-placeholder;
      border-radius: 4px;
      padding: 0 16px;
    }
  }
}