@import '../scss/variables';
@import '../scss/zIndex';

.dsm-c-rp-tool-tips {
  position: fixed;
  pointer-events: none;
  user-select: none;
  padding: 0 $space-10;
  font-size: $fs-S;
  line-height: $space-20;
  background: $color-white;
  color: $color-darkest;
  border-radius: $space-6 - $space-4;
  box-shadow: 0 2px 2px rgba($color-darkest, 0.1);
  max-width: 500px;
  word-break: keep-all;
  word-wrap: normal;
  white-space: pre;
  z-index: $tooltip-index;
  box-sizing: border-box;
  vertical-align: middle;

  &.multi-line {
    padding: $space-8 $space-10;
  }

  &.in-dialog {
    z-index: $dialog-tooltip-index;
  }

  &.auto-wrap {
    white-space: pre-line;
    word-wrap: break-word;
    padding-bottom: $space-6;
    padding-top: $space-6;
    word-break: break-all;
  }
}

.dsm-c-rp-text-overflow,
.dsm-c-rp-ellipsis {
  @include text-overflow;
}
