@import "../scss/_variables";
@import "../scss/_zIndex";

.dsm-c-rp-help-icon {
  display: inline-block;
  .dsm-c-rp-icon {
    color: $color-yellow;
  }
}

.help-popup-tips {
  position: fixed;
  transform: translate($space-6, -50%);
  z-index: $dialog-tooltip-index;
  @include small-drop-shadow;

  transition-property: opacity;
  @include slow-animation;
  opacity: 0;

  &.fade-in {
    opacity: 1;
  }

  & > .left-triangle {
    width: 0;
    height: 0;
    border: $space-10 solid transparent;
    border-right-color: $color-white;
    position: absolute;
    top: 50%;
    left: -$space-10;
    transform: translateY(-50%);
  }

  & > .help-tips-content {
    margin-left: $space-10;
    border-radius: $space-4;
    background: $color-white;
    //min-width: $space-10 * 24;
    color: $font-dark;
    padding: $space-10 + $space-4;
    padding-left: $space-20;
    padding-right: $space-10 + $space-6;

    & > p {
      margin-bottom: $space-10;
      white-space: nowrap;
    }

    & > .help-link {
      color: $color-sysRed;
      cursor: pointer;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}