@import '../scss/variables';

.dsm-c-rp-slider {
  display: inline-block;
  height: 24px;
  &.disabled {
    opacity: 0.3;
    .dsm-c-rp-block-btn {
      pointer-events: none;
    }
  }
}

.dsm-c-rp-slider-click {
  display: inline-block;
  height: 16px;
  position: absolute;
  left: 0;
  top: -6px;
}

.dsm-c-rp-sliding-block {
  height: 2px;
  background-color: var(--slider-background); //$color-blackModeAssistText;
  border-radius: 1px;
  position: relative;
  display: inline-block;
  width: 100%;

  .dsm-c-rp-block {
    height: 100%;
    background-color: $color-sysBlue;
    border-radius: 2px;
    position: relative;
    pointer-events: none;
  }

  .dsm-c-rp-block-btn {
    display: inline-block;
    width: 12px;
    height: 12px;
    background-color: var(--slideCtrlHover); //$color-blackModeAssistText;
    box-sizing: border-box;
    border-radius: 50%;
    position: absolute;
    top: -5px;
    right: -6px;
    pointer-events: auto;
    &:hover {
      background-color: var(--slideCtrlHover);
    }
  }
}

.dsm-c-rp-block-title {
  width: 30px;
  text-align: center;
  background-color: $color-white;
  font-size: $fs-M;
  display: inline-block;
  margin: 0 10px;
  //position: absolute;
  //top: -6px;
  //right: -40px;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Chrome/Safari/Opera */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently not supported by any browser */
}
