@import '~@dsm/scss/_variables.scss';

.dsm-c-tree-rp {
  background-color: $color-white;
  box-sizing: border-box;
  outline: none;
  white-space: nowrap;
  padding: $space-4 0;

  input::selection,
  input::-moz-selection {
    color: $color-white !important;
    background: $color-blue !important;
  }

  .search-empty {
    text-align: center;
    width: 100%;
    padding: 80px 0;

    img {
      display: block;
      width: 60px;
      margin: 0 auto;
    }

    p {
      font-size: 14px;
      color: $font-light;
      opacity: 0.5;
      margin-top: 16px;
    }
  }

  &.max-width-setted {
    overflow: hidden;

    ul,
    ul ul,
    .title {
      max-width: 100%;
    }

    .title {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      display: flex;
      align-items: center;
    }
  }

  ul {
    padding: 0;
    margin: 0;
    min-width: 100%;
    display: inline-block;
    box-sizing: border-box;
    list-style: none;

    li {
      line-height: 1em;
      margin: 0;
      padding: 0 0 0 16px;
    }
  }

  .tree-node-rp {
    color: $font-dark;

    &.highlight {
      .node-bg-checked {
        background-color: $color-sysBlue;
        opacity: 0.15;
      }
    }

    .tree-node-name:hover {
      color: $color-sysBlue;
      cursor: pointer;

      i {
        color: $color-sysBlue;
      }
    }

    .tree-node-name.hover {
      i.icon-expand:hover i {
        color: $color-sysBlue !important;
      }

      &:hover {
        cursor: pointer;

        .name {
          color: $font-dark !important;
        }

        i {
          color: $icon-medium;
        }
      }
    }

    .tree-node-input {
      color: $font-dark;
      border-color: $color-sysBlue;
    }

    .searchKey-highlight {
      color: $color-sysBlue;
    }
  }

  .node-bg-selected {
    background-color: $color-greyBlueActive !important;
    opacity: 1 !important;
  }

  .node-bg-hover {
    background-color: $color-greyBlueActive !important;
    opacity: 0.5 !important;
  }

  .node-bg-drop-over {
    background-color: $color-sysBlue !important;
    opacity: 0.2 !important;
  }

  .selected {
    &:focus {
      .name {
        color: $color-sysBlue;
      }

      .title {
        i {
          color: $color-sysBlue;
        }
      }
    }
  }

  &.is-finding-interaction-target {
    .tree-node .tree-node-name:hover {
      .is-finding-interaction-target-node {
        border-color: $color-blue;
        display: block;
        z-index: 1;
      }
    }
  }
}

.dark-tree {
  background-color: $color-blackModeBackground;

  .tree-node {
    color: $color-blackModeAssistText;

    .tree-node-input {
      color: $color-white;
      border-color: $color-blue;
    }

    .searchKey-highlight {
      color: $color-blackModeText;
    }

    &.highlight {
      .node-bg-checked {
        background: rgba(255, 255, 255, 0.04);
        opacity: 1;
      }
    }

    .tree-node-name {
      i {
        color: $color-blackModeAssistText;
      }

      .title {
        &:hover {
          .name {
            color: $color-blackModeText !important;
          }
        }
      }

      &:hover {
        color: $color-blackModeAssistText;

        i {
          color: $color-blackModeAssistText;
        }
      }
    }
  }

  .node-bg-selected {
    background: hsla(0, 0%, 100%, 0.08) !important;
    opacity: 1 !important;
  }

  .selected {
    &:focus {
      .name {
        font-weight: bold;
        color: $color-white;
      }

      .title {
        i {
          color: $color-blackModeAssistText;
        }
      }
    }

    &:hover {
      .title {
        .name {
          color: $color-white;
        }
      }
    }
  }
}

.spec-dark-tree {
  background-color: $color-blackModeUnder;

  .selected {
    &:focus {
      .name {
        font-weight: normal;
      }
    }
  }
}
