@import "~@dsm/scss/_variables";

.error-boundary {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--bgWorkspaceBar);
  color: var(--textColor);

  .error-boundary-img {
    width: 200px;
    height: 200px;
  }

  .error-boundary-title {
    line-height: 36px;
    font-size: 24px;
    font-weight: 700;
    margin-top: 16px;
  }

  .error-boundary-info {
    line-height: 22px;
    color: var(--textColorImportant);
    margin-top: 8px;
    font-size: 14px;

    & a {
      color: $color-sysBlue;
    }
  }

  .error-boundary-submit {
    margin-top: 32px;
    background-color: $color-sysBlue;
    color: #fff;
    margin-bottom: 146px;
  }
}
