@import '../../scss/variables';

.dsm-c-link {
  text-decoration: none;
  outline: 0;

  &:hover {
    text-decoration: underline;
  }

  &.dsm-c-link-basic {
    color: $color-text;

    &:hover {
      color: $color-sysRed;
    }
  }

  &.dsm-c-link-red {
    color: $color-sysRed;
  }

  &.dsm-c-link-blue {
    color: $color-deepBlue;
  }

  &.dsm-c-link-hover-blue {
    color: $color-text;

    &:hover {
      color: $color-deepBlue;
    }
  }
}

.link-disabled {
  opacity: 0.2;
  cursor: default;
  pointer-events: none;
}