@import '../../../scss/variables';

.dsm-c-popover-fix-content {
  position: fixed;
  left: 100%;
  width: max-content;
  top: 0;
  transform: translate(-50%, -100% - 10);
  background-color: rgba($color: $color-black, $alpha: .85);
  border-radius: $space-4;
  padding: $space-8;
  max-width: 340px;
  z-index: 10;
  color: $color-white;

  &-arrow {
    position: absolute;
    border: $space-4 solid;
    border-color: transparent rgba($color: $color-black, $alpha: .85) rgba($color: $color-black, $alpha: .85) transparent;
    left: 50%;
    top: 100%;
    transform: translate(-50%, -50%) rotate(45deg);
    border-radius: 1px;
    box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, .1);
    outline: 10px solid transparent;
  }

  &-mask {
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    height: 50%;
    opacity: 0;
  }
}