@import '../../../../scss/variables';

.rp-free-invitation {
  margin-top: $space-24;

  .title {
    font-weight: 500;
    line-height: 22px;
    margin-bottom: $space-8;
  }

  .invitation-link-info {
    .desc {
      font-size: 12px;
      color: $color-grey;
      height: 20px;
      line-height: 20px;
      margin-top: $space-8;
    }

    .invitation-link-wrap {
      position: relative;
      padding-right: 110px;

      span {
        display: block;

        .dsm-c-input {
          width: 100%;
          margin-right: 0;
        }
      }

      .button {
        position: absolute;
        right: 0;
        top: 0;
      }
    }
  }

  .invitation-btn-wrapper {
    display: flex;
    justify-content: center;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 30px;
    z-index: 1;

    .dsm-c-button:disabled {
      opacity: 1;
      background: $color-grey;
      color: $color-white;
    }
  }
}