@import '../../scss/variables';

.dsm-c-radio {
  display: inline-block;
  position: relative;
  margin: 6px;
  height: 22px;
  line-height: 22px;
  user-select: none;
  cursor: default;


  .radio-viewer,
  .radio-text,
  .radio-desc {
    box-sizing: border-box;
    vertical-align: middle;
  }

  .radio-viewer {
    display: inline-block;
    width: 14px;
    height: 14px;
    border: 1px solid $color-darkGray;
    border-radius: 50%;
    background-color: transparent;
    position: relative;
    vertical-align: text-bottom;

    &::after {
      content: '';
      display: inline-block;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      position: absolute;
      top: 3px;
      left: 3px;
      background-color: #686b6c;
      transform: scale(0);
      opacity: 0;
      transition: all .1s cubic-bezier(.12, .4, .29, 1.46) .1s;
    }
  }

  .radio-checked {

    &::after {
      opacity: 1;
      transform: scale(1);
      transition: all .1s cubic-bezier(.12, .4, .29, 1.46) .1s;
    }
  }

  .radio-text {
    margin-left: 10px;
    font-size: 14px;
    color: $color-text;
  }

  &.c-radio-disabled {
    opacity: .3;

    .radio-text {
      color: $color-grey;
    }
  }

  &.c-radio-dark {
    .radio-viewer {
      border-color: $color-blackModeText;
      background: $color-blackModeUnder;

      &::after {
        background-color: $color-white;
      }
    }
  }
}