@import "../scss/variables";

.dsm-c-rp-tag {
  display: inline-block;
  box-sizing: border-box;
  height: 28px;
  line-height: 26px;
  background-color: var(--tagMainBgColor);
  border-radius: 2px;
  border: 1px solid var(--tagMainBorderColor);
  padding: 0 8px;
  color: var(--tagMainTextColor);

  &>span {
    display: inline-block;
    line-height: 26px;
    font-size: 13px;
  }

  &>*+* {
    margin-left: 5px;
  }

  &.activated {
    border: 1px solid var(--tagMainActiveBorderColor);
    box-shadow: 0 0 0 1px var(--tagMainActiveBorderColor);
    background-color: var(--tagMainActiveBgColor);
    color: var(--tagMainActiveTextColor);
  }
}