@import "../scss/_variables";

.dsm-c-rp-icon-label {
  height: $space-20 + $space-4;
  line-height: $space-20 + $space-4;
  text-align: left;
  box-sizing: border-box;
  user-select: none;

  & > * {
    float: left;
    color: $font-dark;
    font-size: $fs-L;
   
    &:first-child {
      margin-right: $space-4;
      pointer-events: none;
    }
  }

  &:hover {
    & > * {
      color: $color-sysRed;
    }

    & > .icon {
      & > .dsm-c-rp-icon {
        color: $color-sysRed !important;
      }
    }
  }

  &.disabled {
    pointer-events: none;
    color: $icon-medium;
  }
}