@import '../../scss/variables';
@import '../../scss/z-index';

.dsm-c-rp-tool-tips {
  position: fixed;
  pointer-events: none;
  user-select: none;
  padding: 0 $space-10;
  font-size: $fs-S;
  line-height: $space-20;
  background: $color-white;
  color: $color-darkest;
  border-radius: $space-6 - $space-4;
  box-shadow: 0 2px 8px rgba($color-black, .15);
  max-width: 500px;
  word-break: keep-all;
  word-wrap: normal;
  white-space: pre;
  z-index: 3000; //$tooltip-index;
  box-sizing: border-box;
  vertical-align: middle;

  &.in-dialog {
    z-index: 6000; //$dialog-tooltip-index;
  }
}