@import '../../../../scss/variables';

.dsm-c-pagination-prev,
.dsm-c-pagination-next {
  display: inline-block;
  min-width: 32px;
  height: 32px;
  color: $color-text;
  line-height: $space-30;
  text-align: center;
  vertical-align: middle;
  list-style: none;
  cursor: pointer;
  background-color: $color-white;
  border: 1px solid $color-greyBlueBorder;
  border-radius: 2px;
  box-sizing: border-box;
  outline: none;

  &:hover,
  &:focus {
    .dsm-c-icon {
      color: $color-sysRed;
    }

    border-color: $color-sysRed;
  }

  &.disabled {
    cursor: not-allowed;

    .dsm-c-icon {
      opacity: .25;
    }

    &:hover,
    &:focus {

      .dsm-c-icon {
        color: $color-iconGrey;
        cursor: not-allowed;
      }

      border-color: $color-greyBlueBorder;
    }
  }
}

.dsm-c-pagination-prev {
  margin-right: $space-8;
}


.dsm-c-pagination {
  &.red {

    .dsm-c-pagination-prev,
    .dsm-c-pagination-next {

      &:hover,
      &:focus {
        .dsm-c-icon {
          color: $color-sysRed;
        }

        border-color: $color-sysRed;
      }

      &.disabled {
        cursor: not-allowed;

        .dsm-c-icon {
          opacity: .25;
        }

        &:hover,
        &:focus {

          .dsm-c-icon {
            color: $color-iconGrey;
            cursor: not-allowed;
          }

          border-color: $color-greyBlueBorder;
        }
      }
    }

  }

  &.blue {

    .dsm-c-pagination-prev,
    .dsm-c-pagination-next {

      &:hover,
      &:focus {
        .dsm-c-icon {
          color: $color-deepBlue;
        }

        border-color: $color-deepBlue;
      }

      &.disabled {
        cursor: not-allowed;

        .dsm-c-icon {
          opacity: .25;
        }

        &:hover,
        &:focus {

          .dsm-c-icon {
            color: $color-iconGrey;
            cursor: not-allowed;
          }

          border-color: $color-greyBlueBorder;
        }
      }
    }

  }
}