.dsm-c-rp-tab-panel {
  flex: initial;
  background: #505050;
  user-select: none;
  display: flex;
  flex-direction: column;
  & > .tab-bar {
    height: 30px;
    display: flex;
    flex-wrap: nowrap;
    line-height: 30px;
    background: #555;
    color: #ddd;
    border-bottom: 1px solid #202020;
    margin-bottom: -1px;
    z-index: 1;
    & > * {
      flex: 1;
      min-width: 50px;
      padding: 0 5px;
      text-align: center;
      font-size: 13px;
      background: transparent;

      &.selected {
        background: #404040;
        color: #fff;
      }
    }
  }

  & > .content {
    flex: 1;
    overflow: hidden;
  }
}