@import '../../scss/variables';
@import '../../scss/z-index';

.dsm-c-message {
  position: fixed;
  top: 80px;
  min-width: 300px;
  max-width: 660px;
  padding: $space-30 50px;
  text-align: center;
  background-color: $color-white;
  box-shadow: 0 3px $space-10 0 rgba(0, 0, 0, .15);
  left: 50%;
  transform: translateX(-50%);
  z-index: z-index('message');

  .dsm-c-icon {
    cursor: unset;
  }

  .text {
    margin-top: $space-20;
    line-height: 1.15;
    font-size: 14px;
    user-select: text;
  }

  .info-text {
    width: 16px;
    height: 16px;
    text-align: center;
    line-height: 16px;
    color: $color-white;
    border-radius: 50%;
    background-color: #65d505;
    display: inline-block;
  }
}