@import '../../scss/variables';
@import '../../scss/z-index';

@keyframes dialog-fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 100%;
  }
}

.dsm-dialog-full-screen {
  position: fixed;
  z-index: 3000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $color-white;

  &.ie {
    .dsm-c-pop {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .dsm-c-bg {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 3000;
    width: 100%;
    height: 100%;
  }

  .dsm-dialog-full-screen-pop {
    position: absolute;
    z-index: 3001;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    box-shadow: 0 15px $space-30 0 rgba(0, 0, 0, .15);
    padding: 40px $space-40 $space-30;
    box-sizing: border-box;
    animation: dialog-fade-in .3s ease-in-out;

    .dsm-c-pop-title {
      margin-bottom: $space-20;
    }

    .dsm-c-pop-content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      padding: 0;
      margin-bottom: $space-20;
    }

    .close-icon {
      position: absolute;
      top: $space-32;
      right: $space-32;
      cursor: pointer;
      z-index: 1;
    }
  }
}