@import '../../../scss/variables';

.tree-node {
  box-sizing: border-box;
  position: relative;
  padding: 2px 0;

  .node-bg {
    height: 32px;
    position: absolute;
    right: 0;
    top: 0;
  }

  .tree-node-input {
    //width: 100%;
    height: 32px;
    position: relative;
    padding: 0 8px;
    border: none;
    border-bottom: 1px solid $color-sysBlue;
    outline: none;
    background: none;
    box-sizing: border-box;
    user-select: auto;
  }

  .tree-node-name {
    display: flex;
    height: 32px;
    line-height: 32px;
    position: relative;
    outline: none;
    box-sizing: border-box;
    align-items: center;
    width: 100%;

    .dsm-c-checkbox {
      float: left;
      line-height: 29px;
    }

    .is-finding-interaction-target-node {
      height: 32px;
      position: absolute;
      right: 0;
      top: -2px;
      border: 2px solid transparent;
      display: none;
    }

    .icon-expand {
      &.invisiable {
        visibility: hidden;
      }

      line-height: normal;
      height: inherit;
      display: flex;
      align-items: center;
    }

    .right-icon {
      float: right;
      box-sizing: border-box;
      line-height: normal;
      margin: auto 8px;
    }

    .suffix-icon {
      margin-left: 8px;
    }

    .options-icon {
      margin-left: 8px;
    }

    .title {
      border: 1px solid transparent;
      flex: 1;
      height: 100%;
      overflow: hidden;
      line-height: 30px;

      i {
        margin-right: 5px;
        vertical-align: text-bottom;
        line-height: 16px;
      }

      .name {
        position: relative;
        word-break: break-all;
        white-space: pre-wrap;

        .group-num {
          opacity: .7;
          font-weight: normal;
        }
      }
    }

    &.dullness {
      .title {
        color: $color-darkGray;
      }
    }

    &.lowlight {
      .title {
        opacity: .5;
      }
    }
  }

  &.tree-node-is-dragging {
    opacity: .5;

    .tree-node-is-dragging-bg {
      position: absolute;
      min-width: calc(100% + 2px);
      height: calc(100% + 2px);
      top: 0;
      right: -1px;
      bottom: -2px;
      z-index: 4;
    }
  }

  &.tree-node-drop-at {

    &-before,
    &-after {
      &:before {
        display: inline-block;
        content: '';
        width: 100%;
        height: 2px;
        background-color: $color-sysBlue;
        position: absolute;
        left: 0;
      }

      &:after {
        display: inline-block;
        content: '';
        width: 8px;
        height: 8px;
        border-radius: 8px;
        background-color: $color-sysBlue;
        position: absolute;
        left: 0;
      }
    }

    &-before {
      &:before {
        top: -1px;
      }

      &:after {
        top: -4px;
      }
    }

    &-after {
      &:before {
        bottom: -1px;
      }

      &:after {
        bottom: -4px;
      }
    }

    &-child {
      &>.tree-node-name {
        .title {
          border: 1px solid $color-sysBlue;
          z-index: 1;
        }

        &:after {
          display: inline-block;
          content: '';
          width: 8px;
          height: 8px;
          border-radius: 8px;
          background-color: $color-sysBlue;
          position: absolute;
          left: 32px;
          bottom: -5px;
          z-index: 1;
        }
      }
    }
  }

  &.tree-node-cutted {
    opacity: .5;
  }

  &>.has-interaction {
    width: 0;
    height: 0;
    position: absolute;
    border-width: 3px;
    border-style: solid;
    border-color: $color-deepBlue transparent transparent $color-deepBlue;
    margin: 0;
  }

  &>.isInteractionTarget {
    height: 32px;
    position: absolute;
    right: 0;
    top: -2px;
    border: 2px solid $color-deepBlue;
    background: rgba($color-deepBlue, .4);
  }

  &>.interaction_target_node {
    height: 32px;
    position: absolute;
    right: 0;
    top: -2px;
    background: transparent;
    pointer-events: none;
  }

  &>.interaction-highlight {
    height: 30px;
    position: absolute;
    right: 0;
    top: 0;
    border: 2px solid $color-sysRed;
    pointer-events: none;
  }
}