@import "../scss/variables";

.dsm-c-rp-search-box {
  display: flex;

  padding: 0 12px;
  align-items: center;
  height: 40px;
  line-height: 40px;
  box-sizing: border-box;
  position: relative;

  .search-box-icon {
    &.show-menu:after {
      content: " ";
      border: 3px solid transparent;
      border-top: 3px solid $color-greyBlueBorder;
      width: 0;
      height: 0;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &:after {
    content: "";
    width: 100%;
    bottom: 0;
    left: 0;
    position: absolute;
    border-bottom: 1px solid var(--borderColorBreakMain);
  }

  &.border {
    &::after {
      content: "";
      width: 100%;
      height: 100%;
      bottom: 0;
      left: 0;
      position: absolute;
      border: 1px solid var(--borderColorBreakMain);
      border-radius: 4px;
      z-index: -1;
    }

    &.focus {
      &:after {
        border: 1px solid $color-sysBlue;
      }
    }
  }

  &.light {
    &:after {
      border-bottom: 1px solid $color-greyBlueBorder;
    }
    .dsm-c-rp-icon {
      color: $icon-medium;
    }
  }

  & > .search-box-content {
    width: 100%;
    height: 100%;
    .search-box-icon > .font-icon {
      margin-left: 17px;
      margin-right: 8px;
    }
  }

  &.focus {
    &:after {
      border-bottom: 1px solid $color-sysBlue;
    }
  }

  & > .search-box-preset-selected {
    max-width: 50%;
    margin-left: $space-10;
    display: flex;
    justify-items: center;
    flex-wrap: nowrap;
    align-items: center;
    color: $font-light;
    font-size: 12px;
    & > span {
      text-overflow: ellipsis;
      height: 16px;
      line-height: 16px;
      margin: $space-10 $space-4 $space-10 0;
      padding: 2px;
      overflow: hidden;
      white-space: nowrap;
      border: 1px solid $font-light;
      border-radius: 2px;
    }
  }

  & > .search-input {
    border: 0 !important;
    flex: 1;
    margin: 0 0.8rem;

    &:hover,
    &:focus {
      border: 0 !important;
    }
  }

  & > img {
    width: 14px;
    height: 14px;
    margin: 0 5px;
    &:first-child {
      pointer-events: none;
    }
    &:last-child {
      opacity: 0.6;
      &:hover {
        opacity: 1;
      }
    }
  }
}
