@import '../../scss/variables';

#dsm-c-top-message-tip-box{
  display: flex;
  flex-direction: column;
  position: fixed;
  left: 50%;
  top: $space-32;
  transform: translateX(-50%);
  min-width: $space-100*$num-4+$space-40*$num-2;
  max-width: 80%;
  z-index: 4001;

  .dsm-c-top-message-tip-close{
    animation: slide-out 0.3s ease-out;
  }

  @keyframes slide-out {
    0%{
      transform: translateX(0);
      opacity: 1;
    }
    100%{
      transform: translateX(100%);
      opacity: 0;
    }
  }
}

.dsm-c-top-message-tip {
  position: relative;
  margin-bottom: $space-8;
  .text-container {
    display: flex;
    align-content: center;
    justify-content: space-between;
    padding: $space-14 $space-32;
    border-radius: $space-6;
    background: $color-white;
    box-shadow: 0 $space-5 $space-30 0 rgba(0, 0, 0, $opacity-15);

    .text-info {
      line-height: $space-28;
      white-space: nowrap;

      .target-str {
        font-weight: 600;
      }
    }
  }

  .link-b {
    display: block;
    min-width: $space-48;
    padding: 0 $space-10;
    border-radius: $space-4;
    line-height: $space-28;
    transition: all .2s;
    white-space: nowrap;


    &:hover {
      background: $color-greyBlueActive;
      text-decoration: none;
    }
  }

  .close-btn {
    position: absolute;
    right: $space-6+$space-1;
    top: $space-6;
  }
}
