@import '../scss/variables';

.dsm-c-rp-tree {
  font-size: $fs-M;
  flex: 1;
  overflow: hidden;
  color: var(--treeTextColor);
  .main-content {
    &:not(.remove-hover):hover {
      color: var(--treeTextColorHover);
    }

    &.selected {
      background: var(--treeBgSelected);
      color: var(--treeSelectedTextColor);
      font-weight: bold;
    }

    .arrow > .dsm-c-rp-icon {
      color: $icon-medium;
    }
  }

  .tree-content-flat {
    height: 100%;
    outline: none;
    border: 0;
  }

  .rp-auto-size-list {
    user-select: none;
    outline: none;
    height: 100%;
    width: 100%;
    position: relative;
    overflow-y: overlay !important;
    @include scrollbar();

    &.firefox {
      scrollbar-width: none;
    }
  }

  .tree-content {
    display: inline-block;
    min-width: 100%;
    overflow: hidden;

    .tree-item {
      transform: translateZ(0);
      .main-content {
        display: flex;
        min-width: 100%;
        line-height: 26px;
        height: 26px;
        white-space: nowrap;
        transform: translateZ(0);
        border: 2px solid transparent;
        &:not(.main-content-flat) {
          @include fast-animation;
        }
        &:hover {
          //color: $color-blackModeText;
        }
        &.selected {
          //background: rgba($color-white, 0.08);
          //color: $color-blackModeText;
          font-weight: bold;
        }
        &.marked {
          color: $color-sysBlue;
        }

        .button {
          flex: initial;
          width: 16px;
          //margin-right: 4px;
          margin-left: -2px;
          margin-right: 2px;
          display: flex;
          justify-content: center;
          align-items: center;
          &.leaf {
            pointer-events: none;
            opacity: 0;
          }
          .arrow {
            //color: $color-blackModeAssistText;
            &:not(.arrow-flat-item) {
              transition-property: transform;
              transition-duration: 0.15s;
            }
            &.expand {
              transform: rotate(0);
            }
            &.collapse {
              transform: rotate(-90deg);
            }
          }

          &.editing {
            opacity: 0;
            pointer-events: none;
          }
        }
        .title {
          flex: 1;
          position: relative;
          display: flex;
          align-items: center;
        }
      }
    }
    .dropMoveBox {
      position: absolute;
      display: flex;
      align-items: center;
      &.flat-tree-dropMoveBox {
        z-index: 1;
      }
      &:before {
        display: inline-block;
        content: '';
        width: 100%;
        height: 2px;
        background-color: $color-sysBlue;
        position: absolute;
      }

      &:after {
        display: inline-block;
        content: '';
        width: 6px;
        height: 6px;
        border-radius: 6px;
        background-color: $color-sysBlue;
        position: absolute;
      }
    }
    .isOver {
      &:before {
        background-color: $color-sysRed;
      }
      &:after {
        background-color: $color-sysRed;
      }
    }
  }
  
  &.can-be-interaction-target {
    .can-be-interaction-target-node > .main-content:hover {
      border: 2px solid $color-sysRed !important;
    }
  }

  &.can-show-highlight-border {
    .highlight-target-node-border > .main-content:hover {
      border: 2px solid $color-sysBlue !important;
    }
  }
}
