@import '../scss/variables';

/* theme1 */
.dsm-c-rp-button {
  box-sizing: border-box;
  height: $space-30;
  color: $color-blackModeAssistText;
  border: 0;
  background-color: $color-blackModeBackground;
  outline: 0;
  cursor: pointer;
  margin: 0;
  padding: 0;
  white-space: nowrap;
  line-height: 100%;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.7;
  }

  &:disabled {
    opacity: 0.3;
    cursor: default;
    pointer-events: none;
  }

  /* theme2 */
  &.dsm-c-rp-button-border {
    color: $color-sysRed;
    border: 1px solid $color-sysRed;
    background-color: $color-white;

    &:hover {
      color: #ff5476;
      border: 1px solid #ff5476;
      background-color: $color-greyBlueActive;
    }

    &:active {
      color: #ff365e;
      border: 1px solid #ff365e;
      background-color: $color-greyBlueActive;
    }
  }

  /* theme3 */
  &.dsm-c-rp-button-grey-border {
    color: $color-blackModeText;
    border: 1px solid $color-sysBlue;
    background-color: $color-sysBlue;

    &:hover {
      border: 1px solid rgba($color: $color-sysBlue, $alpha: 0.8);
      background-color: rgba($color: $color-sysBlue, $alpha: 0.8);
      opacity: 1;
    }

    &:active {
      border: 1px solid rgba($color: $color-sysBlue, $alpha: 0.8);
      background-color: rgba($color: $color-sysBlue, $alpha: 0.8);
      opacity: 1;
    }
  }

  /* theme4 */
  &.dsm-c-rp-button-icon {
    color: var(--btnNoneText);
    border: 1px solid $color-blackModeAssistText;
    background-color: transparent;
    padding: 0 4px;
    font-size: 12px;
    &:hover {
      opacity: 1;
    }
    &:active {
      opacity: 1;
    }
    .dsm-c-rp-icon-tooltip {
      margin-right: 4px;
    }
  }

  /* theme5 */
  &.dsm-c-rp-button-only-text {
    color: var(--buttonOnlyText); //$color-blackModeAssistText;
    font-size: 14px;
    background-color: transparent;
    font-weight: bold;
    &.activated {
      color: $color-sysBlue !important;
    }
  }

  /* theme6 */
  &.dsm-c-rp-button-dialog {
    background-color: transparent;
    font-size: 14px;
    font-weight: bold;
    // color: $font-dark !important;
  }

  //
  &.font-bold {
    font-weight: bold;
  }
}
