@mixin black-mode {
  background: $color-blackModeUnder !important;
  color: $color-blackModeText !important;
  border-color: $color-blackModeBorder !important;
}

@mixin black-dsm-icon {
  color: $color-blackModeAssistText;

  &:hover {
    color: $color-blackModeText;
    opacity: 1 !important;
  }

  &.dsm-c-icon-disabled,
  &.dsm-c-icon-disabled:hover {
    color: $color-blackModeAssistText !important;
    opacity: .3 !important;
  }
}

%selectListItem {
  background: rgba($color-blackModeWhite, .08);
}

%disableListItem {
  color: rgba($color-blackModeText, .3);
}

%black-dsm-c-checkbox {
  .checkbox-viewer {
    @include black-mode;
  }

  .checkbox-text {
    color: $color-blackModeText;
  }
}

%black-dsm-c-drop {
  .dsm-c-drop {
    @include black-mode;

    .menu-item {
      color: $color-blackModeAssistText;

      &:hover {
        background-color: rgba($color-blackModeWhite, .08);
      }
    }

    .select-item {
      @extend %selectListItem;
      color: $color-blackModeText;

      &:hover {
        @extend %selectListItem;
      }
    }
  }
}

%black-dsm-select {
  .dsm-c-select {
    &-title {
      color: $color-blackModeText;
    }
  }
}

%black-dsm-c-input {
  input {
    color: $color-blackModeText;
    border-bottom-color: $color-blackModeAssistText;
    background: $color-blackModeUnder;

    &:hover {
      border-color: $color-sysBlue;
    }
  }
}