@import '../../scss/variables';

.dsm-c-tree {
  background-color: $color-white;
  box-sizing: border-box;
  outline: none;
  white-space: nowrap;
  padding: 4px 0;

  input::selection,
  input::-moz-selection {
    color: #ffffff !important;
    background: $color-sysBlue !important;
  }

  .search-empty {
    text-align: center;
    width: 100%;
    padding: 30px 0;

    img {
      display: block;
      width: 60px;
      margin: 0 auto;
    }

    p {
      font-size: 14px;
      color: $font-light;
      opacity: .5;
      margin-top: 16px;
    }
  }

  &.max-width-setted {
    overflow: hidden;

    ul,
    ul ul,
    .title {
      max-width: 100%;
    }

    .title {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
  }

  ul {
    padding: 0;
    margin: 0;
    min-width: 100%;
    display: inline-block;
    box-sizing: border-box;
    list-style: none;

    li {
      line-height: 1em;
      margin: 0;
      padding: 0 0 0 16px;
    }
  }

  .tree-node {
    color: $color-black;

    &.highlight {
      .node-bg-checked {
        background-color: #009dff;
        opacity: .15;
      }
    }

    .tree-node-name:hover {
      color: $color-sysRed;
      cursor: pointer;

      i {
        color: $color-sysRed;
      }
    }

    .tree-node-name.hover {
      i.icon-expand:hover i {
        color: $color-sysRed !important;
      }

      &:hover {
        cursor: pointer;

        .name {
          color: $color-black !important;
        }

        i {
          color: $color-darkGray;
        }
      }
    }

    .tree-node-input {
      color: $color-black;
      border-color: $color-sysRed;
    }

    .searchKey-highlight {
      color: $color-sysRed;
    }
  }

  .node-bg-selected {
    background-color: $color-greyBlueActive !important;
    opacity: 1 !important;
  }

  .node-bg-hover {
    background-color: $color-greyBlueActive !important;
    opacity: .5 !important;
  }

  .node-bg-drop-over {
    background-color: $color-deepBlue !important;
    opacity: .2 !important;
  }

  .selected {
    &:focus {
      .name {
        color: $color-sysRed;
      }

      .title {
        i {
          color: $color-sysRed;
        }
      }
    }
  }

  &.is-finding-interaction-target {
    .tree-node .tree-node-name:hover {
      .is-finding-interaction-target-node {
        border-color: $color-sysBlue;
        display: block;
        z-index: 1;
      }
    }
  }
}

.dark-tree {
  background-color: #202735;

  .tree-node {
    color: #99a5be;

    .tree-node-input {
      color: $color-white;
      border-color: $color-sysBlue;
    }

    .searchKey-highlight {
      color: $color-white;
    }

    &.highlight {
      .node-bg-checked {
        background: rgba(255, 255, 255, .04);
        opacity: 1;
      }
    }

    .tree-node-name {
      i {
        color: #99a5be;
      }

      .title {
        &:hover {
          .name {
            color: #eef0f4 !important;
          }
        }
      }

      &:hover {
        color: #99a5be;

        i {
          color: #99a5be;
        }
      }
    }
  }

  .node-bg-selected {
    background: hsla(0, 0%, 100%, .08) !important;
    opacity: 1 !important;
  }

  .selected {
    &:focus {
      .name {
        font-weight: bold;
        color: $color-white;
      }

      .title {
        i {
          color: #99a5be;
        }
      }
    }

    &:hover {
      .title {
        .name {
          color: $color-white;
        }
      }
    }
  }
}

.spec-dark-tree {
  background-color: #283142;

  .selected {
    &:focus {
      .name {
        font-weight: normal;
      }
    }
  }
}