.measure-text-layer {
  top: -100000px;
  word-break: break-all;
  * {
    margin: 0;
    padding: 0;
    pointer-events: none;
  }

  p {
    margin: 0;
    &:first-child {
      margin-top: 0;
    }
  }

  ul {
    list-style: disc;
  }
  ol {
    list-style: decimal;
  }
  ul,
  ol {
    padding-left: 2em;
    li {
      list-style: inherit;
      padding-left: 0.2em;
    }
  }

  &.wrap {
    * {
      white-space: break-spaces;
    }
  }

  &.no-wrap {
    white-space: pre;
    word-break: break-all;
    * {
      display: contents;
      white-space: pre;
      word-break: break-all;
    }
  }

  &.vertical {
    ul,
    ol {
      padding-top: 2em;
      padding-left: unset;
      li {
        padding-top: 0.2em;
        padding-left: unset;
      }
    }
  }

  //&.debugger {
  // top: 50px;
  // left: 10px;
  // background-color: red;
  // opacity: 1;
  //}
}

.global-family-inherit * {
  font-family: inherit !important;
}

.global-color-inherit * {
  color: inherit !important;
}

.global-size-inherit * {
  font-size: inherit !important;
}

.global-bold-inherit * {
  font-weight: inherit !important;
}

.global-align-inherit * {
  text-align: inherit !important;
}

.global-italic-inherit * {
  font-style: inherit !important;
}

.global-underline-inherit * {
  text-decoration-line: inherit !important;
}

.global-strike-inherit * {
  text-decoration-line: inherit !important;
}

.stop-server-img {
