@import '../../scss/variables';
@import '../../scss/z-index';

.dsm-c-tooltip {
  position: relative;
  display: inline-block;
  cursor: default;
  line-height: 1.15;

  .tips {
    position: absolute;
    white-space: nowrap;
    text-align: center;
    background-color: $color-white;
    padding: 0 20px;
    height: 36px;
    line-height: 36px;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, .15);
    z-index: 1000;
    display: none;

    &.size-s {
      padding: 0 10px;
      line-height: 24px;
      height: 24px;
      font-size: $fs-S;
      //border: 1px solid $color-lightGrey;
      box-shadow: 0 2px 2px 2px rgba(0, 0, 0, .1);
    }

    // direction 
    &.down {
      bottom: -44px;

      &.size-s {
        bottom: -34px;
      }
    }

    &.up {
      top: -44px;

      &.size-s {
        top: -34px;
      }
    }

    // align 
    &.center {
      left: 50%;
      transform: translateX(-50%);
    }

    &.left {
      left: 0;
      transform: translateX(0);
    }

    &.right {
      right: 0;
      transform: translateX(0);
    }

    &.cover {
      position: fixed;
      transform: translateX(0),
    }
  }

  &:hover {
    .tips {
      display: block;
      color: $color-text;
    }
  }
}