@import '../scss/variables';

.dsm-c-rp-checkbox {
  display: flex;
  height: 24px;
  width: fit-content;
  align-items: center;
  position: relative;
  cursor: pointer;
  user-select: none;
  color: var(--textColor);

  &.disabled-checkbox {
    opacity: 0.3;
    cursor: default;
  }

  .dsm-c-rp-link {
    vertical-align: middle;
    margin-left: $space-6;
  }

  .dsm-c-rp-checkbox-viewer,
  .dsm-c-rp-checkbox-text {
    box-sizing: border-box;
    vertical-align: middle;
  }

  .dsm-c-rp-checkbox-viewer {
    display: inline-block;
    flex: 14px 0 0;
    width: 14px;
    height: 14px;
    border: 1px solid var(--checkboxDefault);
    border-radius: 2px;
    transition: background-color 0.2s ease-out, border-color 0.2s ease-out;
  }

  &.light {
    // .dsm-c-rp-checkbox-viewer {
    //   background-color: $color-white;
    //   border-color: $icon-medium;
    // }
    .checkbox-checked {
      background-color: $icon-medium;
      border-color: $icon-medium;
    }

    .checkbox-checked::after,
    .showChoseIconWhenUnChecked::after {
      border-color: $color-backgroundGrey;
    }
  }

  .checkbox-checked,
  .showChoseIconWhenUnChecked {
    background-color: var(--checkboxBgDefault);
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    // background-color: $color-sysBlue;
    // &::before {
    //   content: '';
    //   position: absolute;
    //   left: 3px;
    //   top: 5px;
    //   width: 1px;
    //   height: 4px;
    //   border: 0;
    //   border-right: 1px solid $color-backgroundGrey;
    //   background: $color-backgroundGrey;
    //   transform: rotate(-50deg);
    //   transition: all .3s ease;
    // }

    &::after {
      content: '';
      position: absolute;
      // left: 6px;
      // top: 3px;
      width: 3px;
      height: $space-6;
      border: 2px solid var(--checkboxSelected);
      border-left: 0;
      border-top: 0;
      box-sizing: content-box;
      // border-right: 1px solid $color-backgroundGrey;
      // background: $color-backgroundGrey;
      transform: rotate(45deg) translate(-1.2px, -1px);
      transform-origin: center;
      transition: background-color, border-color 0.1s cubic-bezier(0.12, 0.4, 0.29, 1.46) 0.1s; //all .3s ease;
    }
  }

  .showChoseIconWhenUnChecked {
    background-color: $font-light;
  }

  .checkbox-undefined {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      height: 2px;
      width: 8px;
      border: 0;
      background: $color-white;
      transition: all 0.3s ease;
    }
  }

  &.light > .indeterminate {
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 8px;
      height: 8px;
      border: 0;
      background-color: $icon-medium;
      transition: background-color 0.1s cubic-bezier(0.12, 0.4, 0.29, 1.46) 0.1s;
    }
  }

  .dsm-c-rp-checkbox-text {
    margin-left: 8px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    position: relative;
    // winDPRfontSize13px
    &.win:not(.en) {
      // top: 1px;
      line-height: 1;
    }
  }
}
