@import '../../../scss/variables';

.dsm-c-color-picker {
  width: 222px;
  height: 310px;
  border: 1px solid $color-darkGray;
  border-radius: 4px;
  background-color: $color-white;
  position: relative;
  box-sizing: border-box;
  .saturation-panel {
    width: 200px;
    height: 150px;
    position: relative;
    overflow: hidden;
    margin: 10px;
    margin-bottom: 0;
  }

  .saturation-white,
  .saturation-black {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .saturation-white {
    background: linear-gradient(to right, #fff, rgba(255, 255, 255, 0));
  }

  .saturation-black {
    background: linear-gradient(to top, #000, rgba(0, 0, 0, 0));
  }

  .saturation-pointer {
    position: absolute;
    top: 0;
    left: 0;
  }

  .saturation-circle {
    width: 4px;
    height: 4px;
    box-shadow: 0 0 0 1.5px #fff, inset 0 0 1px 1px rgba(0, 0, 0, 0.3), 0 0 1px 2px rgba(0, 0, 0, 0.4);
    border-radius: 50%;
    transform: translate(-2px, -2px);
  }

  .c-hue-wrap,
  .c-alpha-wrap {
    position: relative;
    width: 172px;
    height: 10px;
    margin: 4px 4px 4px 10px;
  }

  .c-hue-wrap {
    background: linear-gradient(to right, #f00 0%, #ff0 17%, #0f0 33%, #0ff 50%, #00f 67%, #f0f 83%, #f00 100%);
  }

  .c-alpha-wrap {
    margin-bottom: 0;
    background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAAMElEQVQ4T2N89uzZfwY8QFJSEp80A+OoAcMiDP7//483HTx//hx/Ohg1gIFx6IcBALl+VXknOCvFAAAAAElFTkSuQmCC')
      0 0 / auto 100%;
    .alpha-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
    }
  }

  .hue-picker,
  .alpha-picker {
    cursor: pointer;
    width: 4px;
    border-radius: 1px;
    height: 8px;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.6);
    background: #fff;
    transform: translateX(-2px);
    position: absolute;
    z-index: 1;
    top: 1px;
  }

  .slider-control {
    position: relative;
  }

  .current-color-wrap {
    position: absolute;
    right: 10px;
    bottom: 0;
    width: 24px;
    height: 24px;
    border-radius: 2px;
    box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.15), inset 0 0 4px rgba(0, 0, 0, 0.25);
    background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAAICAIAAABLbSncAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAChJREFUeNpiPHPmDAMMGBsbw9lMDDgA6RKM%2F%2F%2F%2Fh3POnj1LCzsAAgwAQtYIcFfEyzkAAAAASUVORK5CYII%3D');
    z-index: 2;
    overflow: hidden;
    .current-color {
      width: 100%;
      height: 100%;
    }
  }

  .editable-input {
    margin-top: 8px;
    display: flex;
    padding: 0 10px;
    font-size: 14px;
    label {
      width: 23px;
      text-align: center;
      display: block;
      margin-right: 12px;
      margin-bottom: 0;
      span {
        display: block;
        margin: 3px 0;
        color: $color-grey;
        font-size: $fs-S;
      }
    }
    input {
      border: 1px solid $color-mediumGrey;
      color: $color-text;
      height: 24px;
      width: 100%;
      padding: 0;
      outline: 0;
      font-size: 12px;
    }
    .hex-input {
      margin-right: 18px;
      label {
        width: 47px;
        margin-right: 0;
      }
      input {
        text-align: center;
      }
    }
    .rgba-input {
      display: flex;
      label:last-child {
        margin-right: 0;
      }
    }
  }

  .preset-colors {
    display: flex;
    flex-wrap: wrap;
    padding-left: 10px;
    padding-top: 10px;
    border-top: 1px solid #eee;
  }

  .c-preset-color {
    width: 16px;
    height: 16px;
    margin-right: 10px;
    margin-bottom: 10px;
    border-radius: 2px;
    border: 1px solid #d9d9d9;
    box-sizing: border-box;
  }
}
