@import '../../../scss/variables';

.dsm-c-search-input-history-panel {
  position: absolute;
  top: calc(100% + 2px);
  z-index: 2;
  width: 100%;
  height: auto;
  max-height: 188px;
  padding: 8px 0;
  background: $color-white;
  border-radius: 4px;
  box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, .16);

  .header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    line-height: 20px;
    font-size: 12px;
    padding: 0 12px;
    color: $color-grey;

    .clear:hover {
      color: $color-sysRed;
      cursor: pointer;
    }
  }

  &>.content {
    text-decoration: none;

    li {
      line-height: 30px;
      font-size: 14px;
      color: $color-text;
      padding: 0 20px;
      list-style: none;

      &:hover {
        background: $color-greyBlueActive;
      }
    }
  }
}