.dsm-c-rp-tile-list {
  display: flex;
  flex-wrap: wrap;
  background: #fff;

  & > .list-item-hot {

    display: flex;
    box-sizing: border-box;
    transform: translateZ(0);
  }

  & > .list-item-hot-default {
    margin: 0 0 -1px -1px;
    transition-property: background-color;
    transition-duration: .15s;

    &:hover {
      background-color: #fafafa;
    }

    & > * {
      flex: 1;
    }
  }
}