@import '../../scss/variables';

.tags-manager-wrapper {
  width: 100%;

  .dsm-c-panel {
    // background-color: $color-greyBlueBg;
    background-color: $color-white;
    max-width: 900px;
    min-width: 650px;
    margin: auto;
    box-sizing: border-box;
    border-radius: 6px;
    box-shadow: none;

    .dsm-c-panel-title {
      padding: 0px;
      display: flex;
      justify-content: space-between;
    }

    .dsm-c-panel-content {
      padding: 10px 0;
    }
  }
}
