@import '../../scss/variables';

.shortcut-dialog-module {
  .dsm-c-scrollview {
    & > * {
      &:nth-child(1) {
        height: 66vh;
      }
    }
  }

  .dsm-c-comp-dialog {
    .dsm-c-pop {
      padding-top: 24px;

      .dsm-c-pop-title {
        color: $font-dark;
        font-size: $fs-L;
        font-weight: bold;
      }
    }
  }
}

.shortcut-dialog {
  ul,
  li {
    line-height: 1.6;
    list-style: none;
    padding: 0;
    margin: 0;
    background-color: $color-blackModeText;
    box-sizing: border-box;
  }

  .dsm-c-pop {
    &::-webkit-scrollbar {
      width: 0;
    }
  }
}

.shortcut-dialog-content-zh {
  border: 1px solid $color-greyBlueActive;
  color: $color-black;

  ul,
  li {
    line-height: 1.6;
    list-style: none;
    padding: 0;
    margin: 0;
    background-color: $color-blackModeText;
    box-sizing: border-box;
  }

  i {
    color: $color-iconGreyText !important;
  }

  .shortcut-title {
    border-bottom: 1px solid $color-greyBlueActive;
    padding: 0 16px;
    cursor: pointer;
    font-weight: bold;
    overflow: hidden;

    span {
      display: inline-block;
      padding: 10px 0;

      &:last-of-type {
        float: right;
      }
    }

    .icondown {
      i {
        transform: rotate(90deg) translateY(-3px) translateX(3px);
        transition: all 0.3s ease-in;
      }
    }

    .iconright {
      i {
        transform: rotate(0) translateY(0) translateX(0);
        transition: all 0.3s ease-in;
      }
    }
  }

  .shortcut-child-box {
    height: 0;
    transition: all 0.5s ease;
    overflow: hidden;

    li {
      height: 43px;
      border-bottom: 1px solid $color-blackModeText;
      background-color: $color-white;
      padding: 0 16px;
      overflow: hidden;

      span {
        display: inline-block;
        box-sizing: border-box;
        padding: 10px 0;
        vertical-align: middle;
        cursor: default;

        &:first-of-type {
          text-align: right;
          width: 45%;
          padding-right: 16px;
        }

        &:last-of-type {
          width: 55%;
          border-left: 1px solid $color-blackModeText;
          padding-left: 16px;
        }
      }
    }
  }
}

