@import '../scss/variables';

.dsm-c-rp-list {
  padding: 10px 0;
  background: var(--bgColor);
  overflow: hidden;
  color: var(--textInputColor);
  .list-content {
    overflow: visible;
    height: auto;
    width: auto;
    position: relative;

    & > .list-item {
      padding: 0;
      width: 100%;
      position: relative;
      overflow-y: hidden;
      & > * {
        width: 100%;
        box-sizing: border-box;
        height: 30px;
        line-height: 30px;
        padding: 0 20px;
        text-align: left;
        > * {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
      & > .default-list-item {
        text-align: left;
        > label {
          display: inline-block;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }

      &:hover,
      &:active,
      &.selected {
        background: var(--bgSelected);
        &:hover {
          background: var(--bgHover);
        }
      }

      &.disabled {
        pointer-events: none;
        opacity: 0.3;
      }
      &.drag-start {
        & > * {
          opacity: 0.6;
        }
      }
    }

    & > .separator {
      left: 0;
      width: 100%;
      height: 12px;
      box-sizing: border-box;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        height: 0;
        top: 50%;
        width: 100%;
        border-bottom: 1px solid var(--borderPanel);
      }
    }

    .drag-over-line {
      position: absolute;
      width: 100%;
      height: 0;
      border-top: 2px solid $color-sysBlue;
      box-sizing: border-box;
      &::before {
        content: ' ';
        position: absolute;
        background: $color-sysBlue;
        width: $space-8;
        height: $space-8;
        border-radius: 100%;
        left: 0;
        top: 0;
        transform: translateY(-5px);
      }
    }
  }

  // &.allow-hover {
  //   background: $color-blackModeDark;
  //   .list-item {
  //     &:hover {
  //       background: $color-blackModeUnder;
  //     }
  //   }
  // }

  &.light {
    color: $font-dark;
    background: $color-white;
    .list-content {
      & > .separator {
        &:after {
          border-bottom: 1px solid $color-greyBlueActive;
        }
      }
    }
    .list-item {
      &:hover,
      &:active,
      &.selected {
        background: $color-greyBlueActive;
        &:hover {
          background: $color-greyBlueActive;
        }
      }
    }

    &.allow-hover {
      background: $color-white;
      .list-item {
        &.selected {
          background: $color-greyBlueActive;
          &:hover {
            background: $color-greyBlueActive;
          }
        }
        &:hover {
          background: $color-greyBlueActive;
        }
      }
    }
  }
}
