@import 'src/dsm/scss/variables';

$width: 450px;
$height: 600px;
$headHeight: 40px;
$fontColor: $color-blackModeText;
.client-layout-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  color: $fontColor;
  .wrapper {
    width: $width;
    height: $height;
    background-color: $color-blackModeBackground;
    display: flex;
    flex-direction: column;
  }
  .header {
    height: $headHeight;
    display: flex;
    align-items: center;

    > .title {
      -webkit-app-region: drag;
      flex: 1 1 auto;
      height: 100%;
    }
    > .close {
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 100%;
      color: $color-blackModeAssistText;
      > i {
        width: 18px;
        height: 18px;
        font-size: 16px;
        line-height: 18px;
        display: block;
        text-align: center;
      }
      &:hover {
        opacity: 0.8;
        background-color: $color-sysRed;
        color: $color-blackModeText;
      }
    }
  }
  .body {
    flex: 1 1 auto;
  }
  .doc_macos {
    .header {
      height: 24px;
      > .close {
        display: none;
      }
    }
  }
}
