@import '../scss/variables';
@import '../scss/zIndex';

.dsm-c-rp-select {
  height: 24px;
  min-width: 40px;
  width: fit-content;
  line-height: 24px;
  word-break: keep-all;
  word-wrap: normal;
  display: flex;
  justify-content: space-between;
  user-select: none;
  outline: none;

  cursor: pointer;
  color: $color-blackModeAssistText;

  &.hidden-button {
    min-width: unset;
  }

  &.disabled {
    cursor: default;
    pointer-events: none;
    user-select: none;
    opacity: 0.3;

    & > .select-value {
      label {
        cursor: pointer;
      }
    }
  }

  &.no-border {
    border: none;
  }

  &.light {
    border: none;
    color: $font-dark;

    .dsm-c-rp-icon:hover {
      color: $color-blackModeBorder;
    }

    .select-value {
      color: inherit;
    }
  }

  & > .select-value {
    width: fit-content;
    overflow: hidden;
    white-space: nowrap;
    word-break: keep-all;
    text-overflow: clip;
    color: var(--textInputColor);
    label {
      cursor: pointer;
    }
  }

  & > .input {
    flex: 1;
  }

  & > .button {
    width: 20px;
    position: relative;
    flex: initial;
  }
}

.drop-down-list {
  background-color: var(--bgColor) !important;
}

.select-drop {
  user-select: none;
  position: fixed;
  top: 0;
  left: 0;
  font-size: $fs-M;
  box-shadow: 0 4px 12px rgba($color-darkest, 0.15);
  z-index: $drop-down-index;
  border-radius: $space-4;
  @include medium-animation;
  @include small-drop-shadow;
  overflow: hidden;
  box-sizing: border-box;
  border: 1px solid var(--borderColorBreakMain);

  .list-scroll {
    & > *:first-child {
      overflow-x: hidden !important;
      margin-bottom: 0 !important;
    }
  }

  .list-item {
    min-width: fit-content !important;
    & > * {
      min-width: fit-content !important;
    }
  }

  &.light-drop {
    border: none;
    > .drop-down-list {
      background-color: $color-white !important;
      color: $font-dark;
      .list-item {
        &:hover,
        &.selected {
          background-color: $color-greyBlueActive;
        }
      }
    }
  }

  &.in-dialog {
    z-index: $dialog-drop-down-index;
  }

  &.pop-up {
    transition-property: height, width, opacity;
    top: unset;
  }

  &.pop-down {
    transition-property: height, width, opacity;
  }

  & > * {
    min-width: 40px;
    width: 100%;
  }
}
