@import '../../scss/variables';
@import '../../scss/z-index';

.dsm-c-arrow-point-tip {
  position: relative;
  display: inline-block;
  cursor: default;
  line-height: 1.15;
  color: $color-text;

  .arrow-point {
    width: $space-10;
    height: $space-10;
    background: $color-white;
    bottom: -10px;
    position: absolute;
    transform: rotate(45deg);
    left: 15px;
  }

  .arrow-point-tips {
    position: absolute;
    white-space: nowrap;
    text-align: center;
    background-color: $color-white;
    padding: 0 20px;
    height: 36px;
    line-height: 36px;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
    z-index: 1000;
    border-radius: 4px;
    &.size-s {
      padding: 0 10px;
      line-height: 24px;
      height: 24px;
      font-size: $fs-S;
      //border: 1px solid $color-lightGrey;
      box-shadow: 0 2px 2px 2px rgba(0, 0, 0, 0.1);
    }

    // direction 
    &.down {
      bottom: -40px;

      &.size-s {
        bottom: -44px;
      }
    }

    &.up {
      top: -40px;

      &.size-s {
        top: -44px;
      }
    }

    // align 
    &.center {
      left: 50%;
      transform: translateX(-50%);
    }

    &.left {
      left: 0;
      transform: translateX(0);
    }

    &.right {
      right: 0;
      transform: translateX(0);
    }
  }
}
