@import '../scss/variables';

.dsm-c-rp-color-picker {
  min-height: 24px;
  min-width: 24px;
  color: transparent;
  user-select: none;
  pointer-events: none;
  position: relative;
  font-size: 18px;
  line-height: 24px;
  display: inline-block;
  height: 24px;
  background-color: var(--borderColorBreakMain);
  border-radius: 2px;
  & > .dsm-c-rp-color-indicator {
    pointer-events: all;
    border-radius: 2px;
    overflow: hidden;
    width: 100%;
    min-height: 100%;
    position: absolute;
    left: 0;
    top: 0;

    box-sizing: border-box;
    cursor: pointer;

    & > .color-content {
      border: var(--pickerValueBorderColor);

      &.none {
        border: 1px solid $color-blackModeBorder;
      }

      & > .no-color {
        background: $color-blackModeUnder;
        color: $color-blackModeText;
        text-align: center;
        line-height: 12px;
        height: 100%;
        &:after {
          content: '-';
        }
      }
    }

    &.disabled {
      cursor: default;
      opacity: 0.3;

      & > .color-content {
        border: 1px solid $color-blackModeDark;
        &.light {
          border: 1px solid transparent;
        }

        &.none {
          border: 1px solid #475164;
        }

        & > .no-color {
          color: #475164;
        }
      }
    }

    & > * {
      position: absolute;
      pointer-events: none;
      top: 4px;
      left: 4px;
      right: 4px;
      bottom: 4px;
      &:first-child {
        background: url('../../assets/image/transparent.png') repeat;
        background-size: 10px 10px;
      }
    }
  }

  &.light {
    background-color: transparent;
    .dsm-c-rp-color-indicator > .color-content {
      border-color: $color-blackModeText;
    }
  }
}
