@import "~@dsm/scss/_variables";

.desktop-container {
  height: 100vh;
  background-color: $color-blackModeBackground;

  &.desktop {
    height: calc(100vh - 40px);
    padding-top: 40px;

    &.mac {
      padding-top: 24px;
      height: calc(100vh - 24px);
    }

    & .global-link {
      cursor: default !important;
    }
  }

  & .global-link {
    cursor: pointer;

    &:hover {
      text-decoration: underline;
    }
  }
}
