@import "~@dsm/scss/_variables";
@import "~@dsm/scss/_zIndex";

.dsm-c-rp-color-collector {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: $most-top;
  background-size: 100% 100%;
  cursor: none;

  &-content {
    position: absolute;
    border: 3px solid $color-white;
    box-sizing: border-box;
    box-shadow: inset 0 0 0 1px #B8BBBF, 0 0 0 1px #B8BBBF;
    border-radius: 50%;
    background-color: $color-darkest;
    transform: translate(-50%, -50%);
    color: #FFFFFF;
    overflow: hidden;
    &::after {
      content: '';
      width: 8px;
      height: 8px;
      border: 1px solid #545454;
      outline: 1px solid $color-white;
      position: absolute;
      top: 90px;
      left: 90px;
    }
    &-text {
      width: 100%;
      text-align: center;
      position: absolute;
      top: calc(50% + 30px);
      color: $color-blackModeText;
      opacity: .9;
      & > span {
        font-size: 12px;
        padding: 4px;
        background-color: rgba(0, 0, 0, .8);
      }
      &-sharp {
        padding-left: 4px;
      }
    }
  }
  &-alert {
    &-explain {
      color: $icon-medium;
      margin-top: 15px;
      padding-left: 20px;
      & > li {
        list-style: disc;
      }
    }
    &-button-group {
      text-align: right;

      & > button {
        width: unset !important;
        padding: 0 $space-10;
        min-width: 60px;
      }
    }
  }
}

