.dsm-notice {
  width: fit-content;
  margin: auto;
  z-index: 10000;
  border-radius: 4px;
  background-color: white;
  padding: 20px 35px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 5px 30px rgba(24,31,41,.15);

  &.top {
    top: 60px;
  }

  &.center {
    top: 40%;
    transform: translate(-50%);
  }

  &.bottom {
    bottom: 60px;
  }
}