@import '../../scss/variables';

.dsm-c-colorful-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-size: 16px 16px;
  cursor: pointer;
  font-style: normal;
  color: $color-black;
  user-select: none;
  &:hover {
    color: $color-sysRed;
  }
}
img {
  &.disabled {
    opacity: 0.3;
  }
}
