@import '../../scss/variables';

.dsm-c-textArea-box {
  .extra-box {
    margin-top: $space-10;
  }
  // .dsm-c-comp-dialog .dsm-c-pop .dsm-c-button-wrap .dsm-c-spc-button {
  //   color: $color-sysRed;
  // }
}

.dsm-c-textArea {
  position: relative;
  border: 1px solid $color-mediumGrey;
  // padding: $space-10 0;
  font-size: 12px;
  line-height: 22px;
  word-break: break-all;
  word-wrap: break-word;
  white-space: pre-wrap;

  &:focus-within {
    border-color: $color-sysBlue;
  }

  textarea {
    position: absolute;
    resize: none;
    width: 100%;
    left: 0;
    top: 0;
    height: 100%;
    color: $color-text;
    padding: $space-8;
    overflow: hidden;
    line-height: 22px;
    box-sizing: border-box;
    font-size: 14px;
    outline: none;
    border: none;
    width: 100%;
    word-break: break-word;
    background-color: $color-white;

    &::-webkit-input-placeholder {
      color: $color-mediumGrey;
    }

    &::-moz-placeholder {
      color: $color-mediumGrey;
    }

    &:-ms-input-placeholder {
      color: $color-mediumGrey;
    }
  }

  .bgc-div {
    position: absolute;
    pointer-events: none;
    font-size: 14px;
    line-height: 1.5;
    opacity: 0;
  }

  .div-hide {
    visibility: hidden;
    width: 100%;
    z-index: -1;
    word-break: break-all;
    white-space: pre-wrap;
    text-align: left;
    opacity: 0;
    line-height: 24px;
    min-height: 36px;
    padding-top: $space-8;
  }

  .measure-layer {
    opacity: 0;
    position: absolute;
    overflow: hidden;
    word-break: break-all;
    white-space: pre-wrap;
    pointer-events: none;
    font-size: 14px;
    line-height: 22px;
  }
}
