@import '../../scss/variables';

.dsm-c-zoomer {
  font-size: 14px;
  height: 20px;
  line-height: 20px;
  position: relative;
  display: inline-block;

  .zoom-btn {
    width: 16px;
    height: 16px;
    line-height: 16px;
    text-align: center;
    display: inline-block;
    cursor: pointer;
    box-sizing: border-box;

    &>*:first-child {
      display: block;
      color: $color-white;
    }

    &>*:last-child {
      display: none;
      color: $color-whiteOpacity50;
    }

    &:hover {
      &>*:first-child {
        display: none;
      }

      &>*:last-child {
        display: block;
      }
    }
  }

  &.disableL {
    .zoom-btn:first-child {
      pointer-events: none;
      opacity: .3;
    }
  }

  &.disableR {
    .zoom-btn:last-child {
      pointer-events: none;
      opacity: .3;
    }
  }

  span {
    margin: 0 6px;
    display: inline-block;
    width: 38px;
    text-align: center;
    position: relative;
    color: $color-white;

    &>.dsm-c-input {
      position: absolute;
      left: 0;
      top: 0;
      height: 18px;

      &>input {
        text-align: center;
        line-height: 18px;
      }
    }

    &.editing {
      color: transparent;
    }
  }

  .no-editing {
    cursor: pointer;

    &:hover {
      opacity: .8;
    }

    &::after {
      content: '%';
    }
  }

  .menu-item::after {
    content: '';
  }

  &>.dsm-c-drop {
    min-width: 100%;
    margin-top: $space-4;

    span {
      text-align: left;
      margin: 0;

      &.menu-item {
        padding: 0 $space-20;
      }
    }
  }
}