.dsm-c-tag-button-group {
  .dsm-c-option-button {
    .tag-text {
      max-width: 150px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .dsm-c-icon + .tag-text {
      margin-left: 10px;
    }
  }

  .tag-button-group-setting {
    padding: 0;
    background-color: transparent;
  }
}
