@import '../../../../scss/variables';

.dsm-c-pagination-quick-jumper {
  display: inline-block;
  height: 32px;
  line-height: 32px;
  vertical-align: top;
  box-sizing: border-box;

  .dsm-c-input {
    height: 100%;
    line-height: 30px;
    margin: 0 $space-8;

    input {
      padding: 0 $space-10;
      border-radius: $space-2;
      border-color: $color-greyBlueBorder;
    }
  }
}

.dsm-c-pagination {
  &.red {
    .dsm-c-pagination-quick-jumper {
      .dsm-c-input input {
        &:hover {
          border-color: $color-sysRed;
        }

        &:focus {
          border-color: $color-sysRed;
          box-shadow: 0 0 0 2px rgba(254, 64, 102, .2);
        }
      }
    }
  }

  &.blue {
    .dsm-c-pagination-quick-jumper {
      .dsm-c-input input {
        &:hover {
          border-color: $color-deepBlue;
        }

        &:focus {
          border-color: $color-deepBlue;
          box-shadow: 0 0 0 2px rgba(24, 144, 255, .2);
        }
      }
    }
  }
}