.c-check-select {
  .check-item-box {
    display: flex;
    align-items: center;
    line-height: 30px;
    padding-right: 10px;

    .dsm-c-checkbox {
      padding-left: 10px;
    }

    .check-item-title {
      flex: 1;
      line-height: 1;
      max-width: calc(100% - 79px);
      height: 30px;

      .dsm-c-tooltip {
        width: 100%;

        .tips.down.center.size-s {
          white-space: normal;
          height: fit-content;
          word-break: break-all;
          max-width: 100%;
          left: 0;
          transform: none;
          top: 100%;
        }
      }
    }
  }

  .check-item-box:hover {
    background-color: #ebedee;
  }

  .item-email-wrapper,
  .item-name {
    flex: 1;
    padding-left: 8px;
    white-space: pre;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
    display: inline-block;
    line-height: 30px;

    .item-email {
      color: #969798;
    }
  }

  .item-name,
  .item-email-wrapper {
    position: relative;

    .compare {
      position: absolute;
      visibility: hidden;
      left: 0;
      top: 0;
      transform: translate(-100%, -100%);
    }
  }

  .avatar {
    display: inline-block;
    width: 20px;
    height: 20px;
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
  }

  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}