@import './variables';

// ------------start------------

// 
$light-text: $color-text;
$dark-text: $color-blackModeText;
// 
$light-icon: $color-iconGrey;
$dark-icon: $color-blackModeTextOP7;
// hover
$light-iconHover: $color-sysRed;
$dark-iconHover: $color-white;
// 
$light-textSecondary: $color-grey;
$dark-textSecondary: $color-blackModeAssistText;
// 2(+)
$light-textThird: $color-text;
$dark-textThird: $color-blackModeAssistText;
// 
$light-inputPlaceholder: $font-input-placeholder;
$dark-inputPlaceholder: $color-blackModeBorder;
// /
$light-border: $color-greyBlueBorder;
$dark-border: $color-blackModeBorder;
// 
$light-activeBg: $color-greyBlueActive;
$dark-activeBg: $color-blackModeDark;
// 
$light-mainBg: $color-backgroundGrey;
$dark-mainBg: $color-blackModeBackground;
// 
$light-inputBg: #f2f2f2;
$dark-inputBg: $color-darkest;
// /
$light-toolBg: $color-white;
$dark-toolBg: $color-blackModeUnder;

// ------------end-----------------


// ------------start------------

// 
$light-navBg: $color-blackModeUnder;
$dark-navBg: $color-blackModeDark;
// ()
$light-workspaceBg: $color-greyBlueActive;
$dark-workspaceBg: $color-blackModeBackground;

// ------------end--------------



// 
$light: (text: $light-text,
  icon: $light-icon,
  iconHover: $light-iconHover,
  textSecondary: $light-textSecondary,
  textThird: $light-textThird,
  inputPlaceholder: $light-inputPlaceholder,
  border: $light-border,
  activeBg: $light-activeBg,
  mainBg: $light-mainBg,
  inputBg: $light-inputBg,
  toolBg: $light-toolBg,
  navBg: $light-navBg,
  workspaceBg: $light-workspaceBg,
);

// 
$dark: (text: $dark-text,
  icon: $dark-icon,
  iconHover: $dark-iconHover,
  textSecondary: $dark-textSecondary,
  textThird: $dark-textThird,
  inputPlaceholder: $dark-inputPlaceholder,
  border: $dark-border,
  activeBg: $dark-activeBg,
  mainBg: $dark-mainBg,
  inputBg: $dark-inputBg,
  toolBg: $dark-toolBg,
  navBg: $dark-navBg,
  workspaceBg: $dark-workspaceBg, );

$themes: (light: $light,
  dark: $dark,
);

@mixin theme($initial-theme:null) {

  @each $theme-name,
  $theme-map in $themes {
    $theme-map: $theme-map !global;

    @if ($initial-theme) {
      @if($initial-theme==$theme-name) {

        body[data-theme="#{$initial-theme}"] & {
          @content;
        }
      }
    }

    @else {
      body[data-theme="#{$theme-name}"] & {
        @content;
      }

    }
  }
}

@function themed($key) {
  @return map-get($theme-map, $key);
}

