@import "../../scss/variables";

.dsm-c-rp-color-preset-colors {

  & > .preset-color-row {
    display: flex;
    margin-bottom: $space-8;

    .preset-item {
      width: $space-20;
      height: $space-20;
      box-sizing: border-box;
      border: var(--pickerValueRecentBorder);
      margin-right: 9px;
      cursor: pointer;

      & > * {
        height: 100%;
      }

      .color-content {
        width: 100%;
        height: 100%;
      }

      &:last-child{
        margin-right: 0;
      }
    }

  }
}