@import '../../scss/variables';

.dsm-c-loading-points {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 3001;
  display: flex;
  align-items: center;
  justify-content: center;

  &.mask {
    background-color: rgba($color: $color-white, $alpha: .3);
  }

  &.shadow {
    .loading-container {
      background: $color-white;
      box-shadow: 0px $space-12 $space-30 0px rgba(0, 0, 0, .16);
    }
  }

  .loading-container {
    width: $space-100;
    height: $space-100;
    border-radius: $space-8;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      pointer-events: none;
      width: $space-48;
      height: $space-48;
    }
  }
}