@import '../scss/variables';
@import '~@dsm/scss/_zIndex';

.dsm-c-rp-popup-menu-background {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1999;
  pointer-events: none;
}

.dsm-c-rp-popup-menu {
  position: fixed;
  color: var(--menu-text-color);
  @include small-drop-shadow;
  background: var(--bgColor);
  padding: $space-10 0;
  border-radius: 4px;
  min-width: $space-10 * 16;
  z-index: 2000;
  display: flex;
  flex-direction: column;
  font-size: 13px;
  border: 1px solid var(--borderColorBreakNormal);
  &.in-popup {
    z-index: $dialog-menu-index;
  }

  &.animation {
    @include medium-animation;
    transition-property: opacity;
  }

  & .item {
    line-height: $space-30;
    padding: 0 $space-20;
    text-align: left;
    user-select: none;
    white-space: nowrap;
    transition-property: background-color;
    @include medium-animation;
    position: relative;
    cursor: pointer;

    & .title {
      display: flex;

      &.reverse {
        flex-direction: row-reverse;

        & > .dsm-c-rp-icon-tooltip {
          margin-right: -22px;
        }
      }
    }

    &.link-item {
      padding: 0;
      .title {
        a.link {
          display: flex;
          flex: 1;
          padding: 0 $space-20;
          color: inherit;
          text-decoration: none;
        }
      }
    }

    & .checker {
      display: flex;
      align-items: center;
      width: fit-content;
      position: relative;
      margin-right: -$space-10 + $space-4;
      transform: translateX(-$space-6 * 2);
      flex: initial;
      pointer-events: none;
      .dsm-c-rp-icon {
        color: var(--menuItemIconColor) !important;
      }
    }

    & .checker-no {
      opacity: 0;
      pointer-events: none;
    }

    .menu-item-icon {
      margin-right: $space-6;
    }

    & .text {
      position: relative;
      flex: 1;
      white-space: nowrap;
      pointer-events: none;
    }

    & .shortcut {
      position: relative;
      right: 0;
      margin-left: $space-20;
      font-family: 'Arial Unicode MS', sans-serif;
      pointer-events: none;
      word-spacing: -1px;
      &.mac {
        word-spacing: unset;
        font-family: unset;
        letter-spacing: 2px;
      }
    }

    & .children-arrow {
      width: $space-10 + $space-6;
      height: $space-10 + $space-6;
      right: -($space-10 + $space-6);
      position: relative;
      margin-left: $space-10;
      pointer-events: none;
      font-size: $fs-XL;
      color: $color-blackModeAssistText;
    }

    & .children-menu {
      position: absolute;
      top: -$space-10 - 1px;
      box-sizing: border-box;

      @include small-drop-shadow;
      background: var(--bgColor);
      padding: $space-10 + 1px 0;
      border-radius: 4px;
      min-width: $space-10 * 10;
      display: none;
      flex-direction: column;

      &.left {
        right: 100%;
      }

      &.right {
        left: 100%;
      }
      .children-arrow {
        transform: translateX(-14px);
      }
    }

    &.disabled {
      opacity: 0.3;
      cursor: default;
    }

    &.enabled:hover {
      background-color: var(--bgHover);
      & > .title > .children-menu {
        display: flex;
        border: 1px solid var(--borderColorBreakNormal);
      }
    }

    &.hidden {
      display: none;
    }

    & .content {
      line-height: 20px;
      font-size: $fs-S;
      color: $color-blackModeAssistText;
    }

    &.selected {
      background-color: $color-darkest;
    }
  }
  //themeItemg
  & .item-more-btn {
    & .title {
      & .text {
        & span {
          color: $color-sysBlue !important;
        }
      }
    }
  }
  //themeItem
  & .item-show-title {
    & .title {
      & .text {
        & span {
          color: var(--menu-title-text-color) !important; // $color-blackModeAssistText !important;
        }
      }
    }
    &.disabled {
      opacity: 1;
      cursor: default;
    }
  }
  //themeItem
  & .item-text-ellipsis {
    height: $space-30;
    line-height: normal;
    & .title {
      display: flex;
      line-height: $space-30;
      & .text {
        margin-right: $space-12;
        & span {
          max-width: 134px;
          display: inline-block;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
    &.selected {
      background-color: red;
    }
  }
  //themeItemhover
  & .item-no-hover {
    cursor: default;
    &.disabled {
      opacity: 1;
      cursor: default;
    }
    &.enabled:hover {
      background-color: $color-blackModeDark !important;
      & > .title > .children-menu {
        display: flex;
        border: none;
      }
    }
  }
  //rightNode
  & .right-node {
    position: absolute;
    right: 11px;
  }

  & .separator {
    height: 0;
    margin: $space-6 0;
    border-bottom: 1px solid var(--borderColorBreakNormal);
    user-select: none;
  }

  &.light {
    background-color: $color-white !important;
    color: $font-dark;
    border: none;
    & .item {
      &.enabled:hover {
        background-color: $color-greyBlueActive;
      }
    }
    & .separator {
      border-bottom: 1px solid $color-greyBlueBorder;
    }
    & .children-arrow {
      color: $color-blackModeBorder;
    }
  }
}
