.c-tree-transfer-check-list {
  flex: 1;
  height: 100%;
  border-right: 1px solid #b8bbbf;
  overflow: hidden;
  white-space: nowrap;
  position: relative;
  .search-wrapper {
    margin: 0 8px 8px;
  }
  .check-container {
    height: 100%;
    .check-all {
      line-height: 30px;
      height: 30px;
      .dsm-c-checkbox {
        padding-left: 30px;
      }
    }
    .check-list-box {
      height: 100%;
    }
  }
}
