@import '../../scss/variables';

.dsm-c-icon-plugin {
  display: inline-block;
  width: 24px;
  height: 24px;
  background-size: 16px 16px;
  background-position: center;
  cursor: pointer;
  font-style: normal;
  color: $icon-medium;
  user-select: none;
  opacity: 1;
  border-radius: 4px;
  text-align: center;
  line-height: 24px;

  &:hover {
    background-color: $color-greyBlueActive;
    color: $icon-medium;
  }

  &.dsm-c-icon-orea {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.dsm-c-icon-plugin-disabled {
  cursor: default;
  opacity: .3;

  &:hover {
    color: $icon-medium;
  }
}

.dsm-c-icon-plugin-solid-disabled {
  cursor: default;
  opacity: .35;

  &:hover {
    color: $color-iconGrey;
  }
}

.dsm-c-icon-plugin-solid-disableHover {
  cursor: default;
  pointer-events: none;

  &:hover {
    color: $icon-medium;
  }
}

.dsm-c-icon-plugin-solid-disableHoverColor {
  cursor: pointer;

  &:hover {
    color: $icon-medium;
  }
}