@import '../scss/variables';

.dsm-c-rp-badge {
  position: relative;
  display: inline-block;

  .dsm-c-rp-badge-item {
    position: absolute;
    overflow: hidden;
    right: 0;
    top: 0;
    background-color: $color-sysRed;
    box-sizing: border-box;
    z-index: 1;

    &.right {
      top: 50%;
      transform: translate(calc(100% + 4px), -55%);
    }

    &.right-top {
      transform: translate(6px, -4px);
    }

    &.count {
      min-width: 16px;
      height: 16px;
      border-radius: 16px;
      padding: 0 4px 0 5px;
      font-size: 12px;
      color: $color-white;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &.dot {
      border-radius: 50%;

      &.default {
        width: 8px;
        height: 8px;
      }

      &.mini {
        width: 5px;
        height: 5px;
      }
    }
  }
}
