@import '../scss/variables';

.dsm-c-rp-icon-button {
  display: inline-block;
  box-sizing: border-box;
  height: 24px;
  color: $color-white;
  border: none;
  background-color: transparent;
  outline: 0;
  cursor: pointer;
  margin: 0;
  padding: 0;
  border-radius: 2px;
  text-align: center;
  &.noborder {
    background-color: transparent;
    border: none;
    &.actived {
      color: $color-sysBlue;
    }
  }
  & > .btn-text {
    color: var(--buttonOnlyText) !important; //$color-blackModeAssistText;
  }
  & > .dsm-c-rp-icon-tooltip {
    line-height: 1;
  }
  .dsm-c-rp-icon {
    pointer-events: none;
    opacity: 1;
    @include medium-animation;
  }
  &:hover {
    .font-icon i {
      color: var(--iconHoverColor);
    }
  }
  &.actived {
    color: $color-sysBlue;
    .font-icon i {
      color: $color-sysBlue;
    }
    & > .btn-text {
      color: $color-sysBlue;
    }
  }

  &:disabled {
    opacity: 0.3;
    cursor: default;
    pointer-events: none;
  }
}
