@import "../scss/variables";

.dsm-c-rp-scrollbars {
  position: relative;
  &.autoHide {
    & > .track-bar {
      & > div {
        opacity: 0 !important;
      }
    }

    &:hover {
      & > .track-bar {
        & > div {
          opacity: 1 !important;
        }
      }
    }
  }

  &.scrollbar{
    & > .track-bar {
      opacity: 0 !important;
      & > div {
        opacity: 0 !important;
      }
    }
    &.showbar{
      & > .track-bar {
        opacity: 1 !important;
        & > div {
          opacity: 1 !important;
        }
      }
    }
  }

  &.win {
    & > * {
      z-index: 1;
      &:nth-child(1) {
        z-index: 0;
      }
    }
  }

  &:hover {
    & > .track-bar {
      opacity: 1 !important;
    }
  }

  .track-bar {
    transition-property: opacity;
    transition-duration: .2s;
    transition-timing-function: ease-in;
    > div {
      background-color: rgba($color-blackModeAssistText, .2) !important;
      border-radius: $space-6 / 2;
      cursor: pointer;
      &:hover, &:active {
        background-color: rgba($color-blackModeAssistText, .4) !important;
        opacity: 1 !important;
      }
    }
  }

  .no-tracker {
    pointer-events: none;
  }

  .no-thumb {
    pointer-events: none;
    opacity: 0;
    display: none;
  }

  .scrollbars-vertical-tracker {
    width: $space-6;
    height: 100%;
    right: 0;
    bottom: 0;
    position: absolute;

    &.large {
      width: $space-4 * 3;
    }
  }

  .scroll-bars-vertical-thumb {
    width: $space-6;
    height: 0;
    border-radius: $space-6 / 2;
    &.large {
      margin-left: $space-4 / 2;
      transition-property: background-color, width, border-right-width, margin-left;
      transition-duration: .15s;

      &:active,
      &:hover {
        margin-left: 0;
        width: $space-8;
        border-radius: $space-4;
      }
    }
  }

  .scrollbars-horizontal-tracker {
    bottom: 0;
    right: 0;
    width: 100%;
    height: $space-6;
    position: absolute;

    &.large {
      height: $space-4 * 3;
    }
  }

  .scroll-bars-horizontal-thumb {
    height: $space-6;
    width: 0;
    border-radius: $space-6 / 2;
    &.large {
      margin-top: $space-4 / 2;
      transition-property: background-color, height, border-right-width, margin-top;
      transition-duration: .15s;

      &:active,
      &:hover {
        margin-top: 0;
        height: $space-8;
        border-radius: $space-4;
      }
    }
  }

  &.dark {
    .track-bar {
      > div {
        background-color: rgba($color-darkest, .1) !important;
        &:hover, &:active {
          background-color: rgba($color-darkest, .3) !important;
        }
      }
    }
  }

  &.deep-dark{
    .track-bar {
      > div {
        background-color: rgba($color-darkest, .4) !important;
        &:hover, &:active {
          background-color: rgba($color-darkest, .4) !important;
        }
      }
    }
  }

  .track-bar,
  .scroll-thumb{
    user-select: none;
  }
}