@import '../scss/variables';

.dsm-c-rp-tab-bar {
  position: relative;
  border-bottom: 1px solid var(--borderColorBreakMain);
  box-sizing: border-box;
  color: var(--textColorImportant);
  & > .tab-bar-content {
    position: relative;
    z-index: 1;
    display: flex;
    user-select: none;
    font-size: $fs-M;
    &.horizontal {
      flex-direction: row;

      & .bar-item {
        cursor: pointer;
        padding: 0 $space-10 + $space-6;
        min-width: $space-30;
        &.selected {
          border-bottom: none;
        }

        &.disabled {
          @include disabled;
        }
      }
    }

    &.vertical {
      flex-direction: column;
    }

    & .bar-item {
      text-align: center;
      cursor: pointer;
      white-space: nowrap;
      word-break: keep-all;
      &.selected {
        color: $color-sysBlue;
      }
    }
  }

  & > .tab-bar-select-indicator {
    position: absolute;
    transition-property: all;
    z-index: 2;

    &.effect {
      @include medium-animation;
    }

    .horizontal {
      position: absolute;
      bottom: 0;
      left: 50%;
      width: $space-20 + $space-4;
      height: $space-4 / 2;
      transform: translateX(-50%);
      background: $color-sysBlue;
    }

    .vertical {
      position: absolute;
      top: 0;
      left: -1px;
      bottom: 0;
      width: $space-4;
      background: $color-sysBlue;
    }
  }

  &.light {
    border-bottom-color: $color-greyBlueBorder;
    color: $color-darkest;
  }
}
