@import '../scss/variables';
@import '../scss/zIndex';

.dsm-c-rp-float-panel {
  position: fixed;
  user-select: none;
  z-index: $menu-index;
  border: 1px solid var(--borderPanel);
  border-radius: $space-4;
  overflow: hidden;
  background: var(--bgColorWrap);
  color: var(--textColor);
  outline: none;
  left: 0;
  top: 0;

  @include comp-default-shadow;

  & > .title {
    height: $space-30 + $space-6;
    line-height: $space-30 + $space-6;
    padding: 0 $space-20;
    font-weight: 700;
    user-select: none;
    // border-bottom: 1px solid $color-blackModeDark;
    margin-bottom: $space-10;
    background: var(--bgPanelTitle);
    color: var(--float-panel-title-text-color);

    & > .close {
      display: block;
      width: 14px;
      height: 14px;
      font-size: 14px;
      text-align: center;
      line-height: 14px;
      position: absolute;
      top: 2px;
      right: 2px;
      font-family: mockplus_rp !important;
      font-weight: 400;
      color: var(--iconListTitle);
      &:hover {
        cursor: pointer;
        color: var(--float-panel-close-button-hover-color); // $color-blackModeText;
      }
      &::after {
        content: '\e6e7';
      }
    }
  }

  & > .content {
    position: relative;
    padding-bottom: $space-10;
  }

  &.light {
    background: $color-white;
    border: 0;
    color: $color-darkest;

    .title {
      background: $color-greyBlueActive;
      border-bottom: 1px solid $color-blackModeText;

      .close {
        color: $icon-medium;
        &:hover {
          color: $color-blackModeBorder;
        }
      }
    }
  }
}
