@import '~@dsm/scss/_variables';
@import '~@dsm/scss/_zIndex';

.desktop-title {
  position: fixed;
  top: 0;
  left: 0;
  z-index: $chrome-max-index;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  color: $color-blackModeText;
  background-color: $color-darkest;
  padding: 0 180px;
  box-sizing: border-box;
  // 
  -webkit-app-region: drag;

  &.transparent {
    background-color: transparent;
  }

  &.mac {
    height: 24px;
    padding: 0;
  }
  &:not(.mac){
    .desktop-title-tooltip-content{
      padding-right: 24px;
    }
  }
  .desktop-title-tooltip-content{
    max-width: 546px;
    display: flex;
    align-items: center;
    height: 100%;
    -webkit-app-region: no-drag;
  }

  .desktop-title-label {
    width: 100%;
    text-align: center;
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .desktop-title-input {
    text-align: center;
    color: #eef0f4;
    & input {
      text-align: inherit;
      color: inherit;
    }
  }

  .desktop-title-close {
    overflow: hidden;
    position: absolute;
    right: 0;
    height: 100%;
    display: flex;
    align-items: center;
    -webkit-app-region: none;

    & > .button {
      width: 50px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;

      & .dsm-c-rp-icon {
        color: #c5ccda;
      }

      &:hover {
        background-color: $color-blackModeUnder;

        &.close {
          background-color: $color-sysRed;
          opacity: 0.8;
        }

        & .dsm-c-rp-icon {
          color: $color-blackModeText;
        }
      }
    }
  }
}

.desktop-title-tooltip {
  z-index: $chrome-max-index;
}
