@import "../scss/variables";

.dsm-c-rp-carousel {
  position: relative;
  overflow: hidden;

  .dsm-c-rp-carousel-items {
    position: absolute;
    display: flex;

    &.left,
    &.right {
      flex-direction: column;
    }

    &.transition {
      &.scroll {
        transition-property: transform;
      }

      &.fade {
        transition-property: none;

        // FIXME 
        & .dsm-c-rp-carousel-item {
          opacity: 0;
          transition-property: opacity;
          transition-timing-function: inherit;
          transition-duration: inherit;

          &.changed {
            opacity: 1;
          }
        }
      }
    }

    &.hidden {
      opacity: 0;
    }
  }

  .dsm-c-rp-carousel-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background: linear-gradient(
      360deg,
      $color-blackModeBackground -139.58%,
      rgba($color: $color-blackModeBackground, $alpha: 0) 100%
    );

    &.left {
      top: 0;
      left: 0;
      height: 100%;
      width: 24px;
      flex-direction: column;
    }

    &.right {
      top: 0;
      right: 0;
      height: 100%;
      width: 24px;
      flex-direction: column;
    }

    &.top {
      top: 0;
      left: 0;
      height: 24px;
      width: 100%;
    }

    &.bottom {
      bottom: 0;
      left: 0;
      height: 24px;
      width: 100%;
    }
  }
}
