@import '../../scss/variables';

.c-table {
  width: 100%;
  position: relative;

  &-content {
    position: relative;

    table {
      text-align: left;
      border-collapse: collapse;
      border-spacing: 0;
      table-layout: fixed;
      width: 100%;
      font-size: 14px;

      th,
      td {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        cursor: default;
      }

      .c-table-thead {
        line-height: $space-36;
        background-color: $color-backgroundGrey;

        th {
          font-size: 12px;
          font-weight: 400;
          padding: 0 $space-24;
          color: $color-darkGray;
          cursor: default;
          user-select: none;

          &.c-table-column-first {
            padding-left: 0;
          }

          &.c-table-column-merge {
            background-color: #F4F4F4;
          }

          &.c-table-column-sortable {
            cursor: pointer;

            &:hover {
              background-color: $color-silverGrey;
            }
          }
        }

        .c-table-column-item {
          display: inline-flex;
          max-width: 100%;

          .c-table-column-item-text {
            flex: 1;
            max-width: 100%;
            text-overflow: ellipsis;
            overflow: hidden;
          }
        }
      }

      .c-table-column {
        &-left {
          text-align: left;
        }

        &-center {
          text-align: center;

          &>* {
            margin: auto;
          }
        }

        &-right {
          text-align: right;
        }

      }
    }

    &-row-menu {
      position: absolute;
      left: 0;
      font-size: 13px;

      .dsm-c-drop {
        position: relative;

        li .menu-item {
          padding: 0 $space-20;
        }
      }
    }
  }

  .loading-box,
  .loading {
    position: absolute;
    width: 100%;
    height: 100%;
    min-height: 600px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    transform: none;
  }

  .loading {
    top: 0;
  }


  &-pager {
    position: sticky;
    bottom: 0;
    padding: $space-56 0 $space-44;
    background: $color-white;
  }

  &-tooltip {
    position: absolute;
    left: 0;
    top: 0;
  }
}