.dsm-c-color-alpha {
  width: 100%;
  height: 10px;
  position: relative;
  user-select: none;
  cursor: pointer;

  & > .bg {
    width: 100%;
    height: 100%;
    display: flex;
    background: url("../../../assets/image/transparent.png") repeat;
    background-size: 10px 10px;
  }

  & > .color {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  & > .point {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: transparent;
    border: 2px solid white;
    box-sizing: border-box;
    position: absolute;
    left: 0;
    top: 0;
    transform: translateX(-50%);
    box-shadow: 0 0 2px 2px rgba(0,0,0,0.2);
    user-select: none;

    &:hover {
      opacity: .8;
    }
  }
}