@import '../../../scss/variables';

.dsm-c-popover-content {
  position: absolute;
  left: 100%;
  width: max-content;
  top: 50%;
  transform: translate(-$space-4, -50%);
  background-color: $color-white;
  box-shadow: 0 $space-4 $space-12 0 rgba(0, 0, 0, 0.1);
  padding: $space-16;
  max-width: 340px;
  border-radius: $space-4;
  z-index: 10;

  &-arrow {
    position: absolute;
    border: $space-6 solid;
    border-color: transparent transparent #fff #fff;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
    border-radius: 1px;
    box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, .1);
  }
}