@import '../../../../scss/variables';

.dsm-c-pagination-forward,
.dsm-c-pagination-backward {
  display: inline-block;
  min-width: 32px;
  height: 32px;
  line-height: 32px;
  text-align: center;
  vertical-align: middle;
  list-style: none;
  border-radius: 2px;
  cursor: pointer;
  margin-right: $space-8;
  position: relative;

  svg {
    display: inline-block;
    width: $space-12;
    height: $space-12;
    opacity: 0;
    transition: all .2s;
    vertical-align: -1.25px;
    margin-top: $space-10;
  }

  .dsm-c-pagination-ellipsis {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: block;
    color: rgba(0, 0, 0, .25);
    letter-spacing: 2px;
    opacity: 1;
    transition: all .2s;
    text-align: center;
  }

  &:hover {
    .dsm-c-pagination-ellipsis {
      opacity: 0;
    }

    svg {
      opacity: 1;
    }
  }
}

.dsm-c-pagination {
  &.red {

    .dsm-c-pagination-forward,
    .dsm-c-pagination-backward {
      color: $color-sysRed;
    }
  }

  &.blue {

    .dsm-c-pagination-forward,
    .dsm-c-pagination-backward {
      color: $color-deepBlue;
    }
  }
}