@import '../../scss/variables';

.mobile-dialog {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, .5);
  z-index: 20;

  .mobile-loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    img {
      display: block;
    }
  }

  .mobile-content {
    min-height: 350px;
    background: $color-white;
    border-radius: 16px;
    position: fixed;
    width: 86%;
    left: 7%;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;
    overflow: hidden;
    margin: 0 auto;

    &>img {
      max-width: 100%;
      display: block;
    }

    .contents {
      padding: 0 10px 25px;
      position: relative;
    }

    .title {
      font-size: 18px;
      color: $font-dark;
      margin: 24px 0 15px;
      font-weight: bold;
    }

    .text {
      font-size: 14px;
      color: $font-medium;
      line-height: 150%;

      p {
        font-size: 14px;
        color: $font-medium;
        line-height: 150%;
      }
    }

    .buttons {
      margin: 20px 0;

      button {
        margin: 0 8px;
      }
    }

    .other {
      color: $font-medium;
      font-size: 14px;

      .red {
        color: $color-sysRed;
      }
    }
  }

  &.mobile-dialog-fail {
    .mobile-content {
      padding: 20px 0 10px;
      border-radius: 8px;
      min-height: 0;

      .text {
        text-align: center;
        display: inline-block;
        padding: 0 10px;

        p {
          text-align: left;
          position: relative;
          padding-left: 8px;

          &::before {
            content: '';
            width: 3px;
            height: 3px;
            border-radius: 100%;
            background: $icon-light;
            position: absolute;
            left: 0;
            top: 8.5px;
          }
        }
      }

      .buttons {
        margin-top: 30px;
      }
    }
  }

  @media (max-width: 359px) {
    .mobile-content {
      width: 90%;
      left: 5%;

      .contents {
        .title {
          font-size: 16px;
        }

        .text {
          font-size: 12px;
        }
      }
    }
  }
}