@import '../../../scss/variables';

.dsm-c-pagination-total {
  position: absolute;
  left: 0;
  display: flex;
  align-items: center;
  font-size: 12px;
  color: $color-grey;
  white-space: nowrap;

  .dsm-c-checkbox {
    margin-left: $space-10;
  }

  .total {
    margin-left: $space-24;

    &.show-action-menu {
      margin-left: 0;
    }

    &.overflowHide {
      visibility: hidden;
    }

    .match-str-wrapper {
      line-height: $space-30;
    }
  }


  @media (max-width:1440px) {
    .total {
      display: none;
    }
  }

  .action-btn {
    line-height: 17px;
    display: flex;
    align-items: center;

    .dsm-c-pagination-split-line {
      display: inline-block;
      width: 1px;
      height: 13px;
      margin: 0 $space-10;
      background-color: $color-greyBlueBorder;
    }
  }

  .action-btn-container {
    line-height: 17px;
    display: flex;
    align-items: center;

    .dsm-c-pagination-split-line {
      display: inline-block;
      width: 1px;
      height: $space-16;
      margin: 0 $space-16;
      background-color: $color-greyBlueBorder;

      &.overflowHide {
        visibility: hidden;
      }

      @media (max-width:1440px) {
        &:nth-of-type(3) {
          display: none;
        }
      }
    }

    .btn-container {
      .link-b:not(:first-of-type) {
        margin-left: $space-16;
      }

      .link-b.disabled {
        opacity: .3;
        cursor: default;

        &:hover {
          text-decoration: none;
        }
      }

      .dsm-c-drop {
        position: absolute;
        bottom: 100%;
        padding: $space-10 0;
      }
    }
  }
}