@import '../../../scss/variables';

.rp-free-dialog-header {
  text-align: center;
  margin-bottom: 17px;

  .title {
    font-size: 60px;
    color: $color-white;
    width: 448px;
    height: 51px;
    margin: auto;
  }

  .time {
    color: rgba(255, 255, 255, 0.7);
    font-size: 12px;
    margin-top: 8px;
    text-align: center;
    font-weight: 400;
  }

  .desc {
    line-height: 30px;
    font-size: 18px;
    font-weight: 500;
    color: $color-white;
    text-shadow: 0px 2px 4px 0px rgba(134, 121, 217, 0.5);
    margin: $space-10 0 $space-8;
  }

  .sub-desc {
    font-size: 12px;
    color: $color-white;
    text-shadow: 0px 2px 4px 0px rgba(134, 121, 217, 0.5);
    line-height: 20px;
    opacity: 0.7;
  }
}
