@import "../scss/variables";
@import "~@dsm/scss/_zIndex";

.dsm-c-rp-popup-panel {
  position: fixed;
  color: $color-blackModeText;
  @include small-drop-shadow;
  background: var(--bgColor);
  padding: $space-10 0;
  border-radius: 4px;
  min-width: $space-10 * 16;
  z-index: 2000;
  display: flex;
  flex-direction: column;
  font-size: 13px;
  border: 1px solid var(--borderColorBreakNormal);

  &.in-popup {
    z-index: $dialog-menu-index;
  }

  &.animation {
    @include medium-animation;
    transition-property: opacity;
  }

  &.light {
    background-color: $color-white !important;
    color: $font-dark;
    border: none;

    .dsm-c-rp-number-input,
    .dsm-c-rp-input {
      input[type=text],
      input[type=password] {
          &::-webkit-input-placeholder {
            color: $color-greyText;
          }
    
          &::-moz-placeholder {
            color: $color-greyText;
          }
    
          &:-moz-placeholder {
            color: $color-greyText;
          }
    
          &:-ms-input-placeholder {
            color: $color-greyText;
          }
    
          &::placeholder {
            color: $color-greyText;
          }
        }
      }

  }
}
